import {Component} from "@angular/core";
import {BaseControlComponent} from "app/common/controls/base.component";
import {ControlDefinition} from "app/common/decorators/control-definition";
import { CATEGORY, SUBCATEGORY_PARAGRAPH, PARAGRAPH_CONTROLS } from "./../controls-name-definition";

@Component({
  selector: "bs-paragraph16-control",
  templateUrl: "./paragraph16-control.component.html",
  styleUrls: ["./paragraph16-control.component.scss"]
})
@ControlDefinition(
  {
    type: PARAGRAPH_CONTROLS.paragraph16.type,
    category: CATEGORY.TEXT.displayName,
    subcategory: SUBCATEGORY_PARAGRAPH.displayName,
    displayName: PARAGRAPH_CONTROLS.paragraph16.displayName,
    icon: PARAGRAPH_CONTROLS.paragraph16.icon,
      styles: {
        fontFamily: {
          type: "font-family",
          defaultValue: "Open Sans"
        },
        fontSize: {
          type: "font-size",
          defaultValue: "16px",
          max: 72
        },
        letterSpacing: {
          type: "range",
          defaultValue: 0,
          min: 0,
          max: 60
        },
        align: {
          type: "text-align",
          defaultValue: "left"
        },
        fontColor: {
          type: "color",
          defaultValue: "color-sixth"
        },
        backColor: {
          type: "color",
          defaultValue: "rgba(0, 0, 0, 0)"
        },
        bold: {
          type: "boolean"
        }

      },
      parameters: {
        text: {
          type: "text",
          defaultValue: PARAGRAPH_CONTROLS.paragraph16.displayName
        }
      },
      defaultSize: {
        width: 300,
        height: 200
      }
  }
)
export class Paragraph16ControlComponent extends BaseControlComponent {

  fontWeight() {
    let bold = this.styles["bold"];
    return bold === true ? "600" : "100";
  }

}
