import {Component, EventEmitter, Input, Output} from "@angular/core";


@Component({
  selector: "bs-dm-template-list-view",
  templateUrl: "./dm-template-list-view.component.html",
  styleUrls: ["./dm-template-list-view.component.scss"]
})
export class DmTemplateListViewComponent {

  @Input() selectedField;
  @Output() setFocus = new EventEmitter();
  @Output() changeDisableField = new EventEmitter();

  constructor() { }

  changeField(field: string) {
    this.changeDisableField.emit(field);
  }

}
