import {Component, EventEmitter, Input, Output, ViewChild} from "@angular/core";
import {ModalWebPartsComponent} from "app/common/components/modal-webparts/modal-webparts.component";
import {CultureSettingsService} from "app/common/services/sites/culture-settings.service";
import {UserPreferencesComponent} from "app/common/components/user-preferences/user-preferences.component";
import {UserService} from "app/common/services/sites/user.service";
import {PublishedSiteService} from "app/render/services/published-site.service";

@Component({
    selector: "bs-user-preferences-render",
    templateUrl: "./user-preferences-render.component.html",
    styleUrls: ["./user-preferences-render.component.scss"]
})
export class UserPreferencesRenderComponent {

    public buttons = [];
    public currentUserPreferences: IUserPreferences;
    public formatOptions: ICultureFormatOptions;

    @Input() user: IUserSiteProfile;
    @Input() currentLanguage: IBasicLanguageResource;

    @Output() onUserUpdate = new EventEmitter();
    @ViewChild("siteCultureConfig", { static: true }) private _userPreferencesModal: ModalWebPartsComponent;
    @ViewChild("userPreferences", { static: false }) private _userPreferencesComponent: UserPreferencesComponent;

    constructor(private _cultureSettingsService: CultureSettingsService,
        private _publishedSiteService: PublishedSiteService,
        private _userService: UserService) {

    }

    ngOnInit() {
        this._initialize();
    }

    public onModalButtonClicked(button: string) {
        if ( button === "save" ) {
            this.user.preferences = this._userPreferencesComponent.languageCulture;
            this._userService.createOrUpdateUserPreferences(this.user).then((user: IUserSiteProfile) => {
                this.onUserUpdate.emit(user);
                this._userPreferencesModal.close();
            });
        } else {
            this._userPreferencesModal.close();
        }
    }

    ngOnDestroy() {
        this._cultureSettingsService.userPreferencesStatus.unsubscribe();
    }

    private async _initialize() {
        this.currentUserPreferences = _.cloneDeep(this.user.preferences);
        this._cultureSettingsService.userPreferencesStatus.subscribe( ( state: any ) => {
            if ( state.isOpen ) {
                this._userPreferencesModal.open();
            } else {
                this._userPreferencesModal.close();
            }
        });
        this.buttons = [
            {
                name: "cancel",
                caption: "siteeditor.cultureconfig.CLOSE",
                type: "secondary"
            },
            {
                name: "save",
                caption: "siteeditor.cultureconfig.SAVE"
            }
        ]
    }
}
