import { Injectable } from "@angular/core";
import {RestApiSevice} from "app/common/services/oauth/rest-api.service";
import {Cache} from "./../../common/decorators/cache";

@Injectable()
export class BizagiDataService {

  private endpoints: any = {
    userprofile: "api/bizagi/userprofile",
    stakeholders: "api/bizagi/stakeholders",
    collectionsdata: "api/bizagi/collections/{collectionId}",
    casesdata: "api/bizagi/cases/{processId}",
    searchresultsdata: "api/bizagi/searchresults/{search}",
    inboxcases: "api/bizagi/inboxcases",
    relevants: "api/bizagi/relevants",
    contextnavigationdata: "api/bizagi/navigations/{connectionEntityId}", // list result from an entity
    navigationdata: "api/bizagi/navigationData", // get entity data
    workitemsData: "api/bizagi/cases/{processId}/case/{caseId}/workitems"
  };

  constructor(protected _restApiService: RestApiSevice) {
  }

  @Cache()
  public async getUserProfile(url: string): Promise<IUserProfile> {
    let response =  await this._restApiService.post({
      url: this.endpoints.userprofile,
      body: {
        url: url
      }
    }).toPromise();
    return <IUserProfile>(response).user;
  }

  @Cache()
  public async getStakeholders(url: string): Promise<IStakeholder[]> {
    let response = await this._restApiService.post({
      url: this.endpoints.stakeholders,
      body: {
        url: url
      }
    }).toPromise();
    return <IStakeholder[]>(response.stakeholders);
  }

  public async getCollection(source: string, parameters: ICollectionDataParams): Promise<IBizagiDataResult> {
    return await this._restApiService.post({
      url: this.endpoints.collectionsdata,
      parameters: {
        collectionId: parameters.collectionId
      },
      body: {
        url: source,
        value: parameters.value || undefined,
        xpath: parameters.value ? parameters.xpath : undefined,
        rows: parameters.pagination ? parameters.pagination.rows : 10,
        from: parameters.pagination ? parameters.pagination.from : 0,
        mappingType: parameters.mappingType ? parameters.mappingType.toLowerCase() : undefined
      }
    }).toPromise().catch(error => {
      return null;
    });
  }

  public async getCases(source: string, parameters: ICaseDataParams): Promise<IBizagiDataResult> {
    return await this._restApiService.post({
      url: this.endpoints.casesdata,
      parameters: {
        processId: parameters.processId
      },
      body: {
        url: source,
        value: parameters.value || undefined,
        xpath: parameters.value ? parameters.xpath : undefined,
        rows: parameters.pagination ? parameters.pagination.rows : 10,
        from: parameters.pagination ? parameters.pagination.from : 0,
        mappingType: parameters.mappingType ? parameters.mappingType.toLowerCase() : undefined
      }
    }).toPromise().catch(error => {
      return null;
    });
  }

  public async getWorkitems(source: string, processId: string, caseId: string): Promise<IBizagiCaseWorkitems> {
    return await this._restApiService.post({
      url: this.endpoints.workitemsData,
      parameters: {
        processId: processId,
        caseId: caseId
      },
      body: {
        url: source
      }
    }).toPromise().catch(error => {
      return null;
    });
  }

  public async getSearchResults(source: string, parameters: ISearchDataParams): Promise<IBizagiDataResult> {
    return await this._restApiService.post({
      url: this.endpoints.searchresultsdata,
      parameters: {
        search: parameters.searchId
      },
      body: {
        url: source,
        value: parameters.value || undefined,
        xpath: parameters.value ? parameters.xpath : undefined,
        rows: parameters.pagination ? parameters.pagination.rows : 10,
        from: parameters.pagination ? parameters.pagination.from : 0,
        mappingType: parameters.mappingType ? parameters.mappingType.toLowerCase() : undefined
      }
    }).toPromise().catch(error => {
      return null;
    });
  }

  // Get list result from an entity context
  public async getContextNavigations(source: string, parameters: any,
                                     pagination: IPagination): Promise<IBizagiDataResult> {
    return await this._restApiService.post({
      url: this.endpoints.contextnavigationdata,
      parameters: {
        connectionEntityId: parameters["connectionEntityId"]
      },
      body: {
        url: source,
        value: parameters.value || undefined,
        xpath: parameters.value ? parameters.xpath : undefined,
        connectionType: parameters.connectionType,
        rootConnectionType: parameters.rootConnectionType,
        key: parameters.key,
        navigationLevels: parameters.navigationLevels,
        rows: pagination ? pagination.rows : 10,
        from: pagination ? pagination.from : 0
      }
    }).toPromise().catch(error => {
      return null;
    });
  }

  public async getInbox(source: string, parameters: any): Promise<IBizagiDataResult> {
    return await this._restApiService.post({
      url: this.endpoints.inboxcases,
      parameters: {
      },
      body: {
        url: source,
        filter: parameters.filter || undefined,
        state: parameters.state || undefined,
        orderby: parameters.orderby || undefined,
        rows: parameters.pagination ? parameters.pagination.rows : 10,
        from: parameters.pagination ? parameters.pagination.from : 0

      }
    }).toPromise().catch(error => {
      return null;
    });
  }

  public async getRelevants(source: string): Promise<IBizagiDataResult> {
    return await this._restApiService.post({
      url: this.endpoints.relevants,
      parameters: {
      },
      body: {
        url: source
      }
    }).toPromise().catch(error => {
      return null;
    });
  }

  // Get entity data
  public async getNavigationData(source: string, navigationContext: Array<INavigationContext>): Promise<any> {
    return await this._restApiService.post({
      url: this.endpoints.navigationdata,
      body: {
        url: source,
        navigationContext: navigationContext
      }
    }).toPromise();
  }
}
