import {
  CATEGORY,
  SUBCATEGORY_SQUARE,
  SQUARE_CONTROLS
} from "./../controls-name-definition";
import {Component} from "@angular/core";
import {BaseControlComponent} from "app/common/controls/base.component";
import {ControlDefinition} from "app/common/decorators/control-definition";

@Component({
  selector: "bs-square-control",
  templateUrl: "./square-control.component.html",
  styleUrls: ["./square-control.component.scss"]
})
@ControlDefinition(
  {
    type: SQUARE_CONTROLS.square.type,
    category: CATEGORY.FIGURES.displayName,
    subcategory: SUBCATEGORY_SQUARE.displayName,
    displayName: SQUARE_CONTROLS.square.displayName,
    icon: SQUARE_CONTROLS.square.icon,
    styles: {
       keepSize: {
        type: "boolean",
        defaultValue: false
      },
      color: {
        type: "color",
        defaultValue: "none"
      },
      opacity: {
        type: "range",
        defaultValue: 100,
        min: 0,
        max: 100
      },
      cornerRadius: {
        type: "range",
        defaultValue: 0,
        min: 0,
        max: 50
      },
      border: {
        type: "border",
        defaultValue: "1px solid #000"
      },
   },
    parameters: {
    },
    defaultSize: {
      width: 200,
      height: 204
    },
    hiddenOptions: {
      dataManager: true
    }
  }
)

export class SquareControlComponent extends BaseControlComponent {

  fontWeight() {
    let bold = this.styles["bold"];
    return bold === true ? "600" : "100";

  }
}
