import { Injectable } from "@angular/core";

@Injectable()
export class ListService {
  private _find(element: any, dataStore: Array<any>) {
    for (let index = 0; index < dataStore.length; index++) {
      if (_.isEqual(dataStore[index], element)) {
        return index;
      }
    }

    return -1;
  }

  public moveNext(element: any, dataStore: Array<any>) {
    const index = this._find(element, dataStore);
    const newIndex = index + 1;

    if (newIndex >= dataStore.length) {
      console.log("cannot move more than last item");
    } else {
      dataStore.splice(newIndex, 0, dataStore.splice(index, 1)[0]);
    }
  }

  public movePrev(element: any, dataStore: Array<any>) {
    const index = this._find(element, dataStore);
    const newIndex = index - 1;

    if (newIndex < 0) {
      console.log("cannot move more than first item");
    } else {
      dataStore.splice(newIndex, 0, dataStore.splice(index, 1)[0]);
    }
  }

  public moveToFirst(element: any, dataStore: Array<any>) {
    const index = this._find(element, dataStore);
    const newIndex = 0;
    dataStore.splice(newIndex, 0, dataStore.splice(index, 1)[0]);
    return dataStore;
  }

  public moveToLast(element: any, dataStore: Array<any>) {
    const index = this._find(element, dataStore);
    const newIndex = dataStore.length - 1;
    dataStore.splice(newIndex, 0, dataStore.splice(index, 1)[0]);
    return dataStore;
  }
}
