import { Component, ViewChild, Input, Output, EventEmitter, ElementRef } from "@angular/core";
import { ModalWebPartsComponent } from "app/common/components/modal-webparts/modal-webparts.component";
import { CultureSettingsService } from "app/common/services/sites/culture-settings.service";
import { stateSubscriptions } from "app/editor/site-editor/store/state-subscriptions";
import { Observable } from "rxjs";
import { MenusActionsService } from "app/editor/site-editor/store/menu/menus-actions.service";
import { UserPreferencesComponent } from "app/common/components/user-preferences/user-preferences.component";
import { UserService } from "app/common/services/sites/user.service";
import { Store } from "@ngrx/store";
import { ContextService } from "../../common/services/sites/context.service";
import { Router } from "@angular/router";
import { Global } from "../../common/misc/global";

interface IBreadCrumbPart {
  navigation: INavigationContext
  page: IBasicPage
}

@Component({
  selector: "bs-breadcrumb-render",
  templateUrl: "./breadcrumb-render.component.html",
  styleUrls: ["./breadcrumb-render.component.scss"]
})
export class BreadcrumbRenderComponent {
  @Input() navigationContexts: Array<INavigationContext>;
  @Input() hero: IControl;
  @Input() header: IControl;
  public grid: IGrid;
  public top = 0;
  public pageNavigationList: Array<IBreadCrumbPart> = [];
  @Input() currentLanguage: ILanguageResource;
  private gridSize$: Observable<IGrid>;
  public currentPage: IPage;

  constructor(private _contextService: ContextService, private _router: Router, private _store: Store<IRenderState>,
    private _elementRef: ElementRef) {
    this.gridSize$ = this._store.select((s) => s.grid);
  }

  ngOnInit() {
    this.currentPage = _.cloneDeep(this._contextService.getPage());
    let pages = this._contextService.getPages();
    _.each(this.navigationContexts, (navigationContext: INavigationContext) => {
      let page = _.find(pages, { originalId: navigationContext.originPageId });
      if (this.currentLanguage.resources[page.displayName]) {
        page.displayName = this.currentLanguage.resources[page.displayName];
      }
      this.pageNavigationList.push({ navigation: navigationContext, page: page });
    });
    this.gridSize$.subscribe((grid: IGrid) => {
      $(this._elementRef.nativeElement.children[0]).css("padding-left", grid.boundaries.left);
    });
  }

  ngAfterViewInit() {
    setTimeout(() => {
      if (this.hero.styles.showHeroImage && this.hero.styles.heroImage && this.currentLanguage.resources[this.hero.styles.heroImage.toString()]) {
        this.top = <number>this.hero.styles.heroImageHeight;
      } else {
        this.top = <number>this.header.styles.height;
      }
    });
  }

  public navigate(breadCrumbPart: IBreadCrumbPart) {
    let indexOfNavigation = _.findIndex(this.pageNavigationList, breadCrumbPart);
    let newNavigationContextList = _.slice(this.navigationContexts, 0, indexOfNavigation);
    if (newNavigationContextList.length) {
      this._router.navigate([this._contextService.getSite().url, breadCrumbPart.page.name],
        { queryParams: { "context": Global.jsonToBase64(newNavigationContextList) } });
    } else {
      this._router.navigate([this._contextService.getSite().url, breadCrumbPart.page.name]);
    }
  }
}
