
import { map } from "rxjs/operators";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs/Rx";
import { RestApiSevice } from "app/common/services/oauth/rest-api.service";
import { Cache } from "app/common/decorators/cache";
import { DomSanitizer } from "@angular/platform-browser";
import { UrlObject } from "url";

@Injectable()
export class ImagesService {

  private endpoints: any = {
    saveImages: "api/{projectId}/images/{resourceId}/{id}/{isPreview}",
    getImages: "api/{projectId}/sites/{siteId}/images/{imageName}",
    getImagesBlob: "api/sites/imagesblob",
    deleteImages: "api/{projectId}/images/{id}",
    duplicateImage: "api/{projectId}/images/{resourceId}/{imageIdOrigin}/duplicate/{imageIdDestination}",
    duplicateImages: "api/{projectId}/images/{resourceId}/duplicateMultiple/"
  };

  private pageCache: { [key: string]: IPage };

  constructor(protected _restApiService: RestApiSevice, private _sanitizer: DomSanitizer) {
    this.pageCache = {};
  }

  public deleteImage(projectId: string, imageId: string): Observable<any> {
    return this._restApiService.delete({
      url: this.endpoints.deleteImages,
      parameters: {
        projectId: projectId,
        id: imageId
      }
    });
  };

  public uploadImage(projectId: string, resourceId: string, imageId: string, imageBlob: any, type: string, fileExtension = "png", isPreview = false):
    Observable<string> {
    return this._restApiService.postFile({
      url: this.endpoints.saveImages,
      parameters: {
        resourceId: resourceId,
        projectId: projectId,
        id: imageId,
        isPreview: isPreview
      }
    }, imageBlob, type).pipe(map((response: any) => {
      let imageURL = `api/${projectId}/resources/${resourceId}/images/img_${imageId}.${response.extension}`;
      return imageURL;
    }));
  }

  public async duplicateImage(projectId: string, resourceId: string, imageIdOrigin: string, imageIdDestination: string) {
    let promise = await this._restApiService.post({
      url: this.endpoints.duplicateImage,
      parameters: {
        resourceId: resourceId,
        projectId: projectId,
        imageIdOrigin,
        imageIdDestination
      }
    }).toPromise();

    return promise;
  }

  public async duplicateImages(projectId: string, resourceId: string, images: { [key: string]: string }) {
    let promise = await this._restApiService.post({
      url: this.endpoints.duplicateImages,
      parameters: {
        resourceId: resourceId,
        projectId: projectId
      },
      body: images
    }).toPromise();

    return promise;
  }

}
