import { BaseControlComponent } from "./base.component";
import { ActivatedRoute, Router } from "@angular/router";
import "rxjs/add/operator/toPromise";
import { PageRenderingService } from "app/common/services/sites/page-rendering.service";
import { Global } from "app/common/misc/global";
import { FormType } from "app/render/bizagi-forms/bizagi-forms.component";
import { TranslateService } from "@ngx-translate/core";
import { DefaultValues } from "../misc/default-values";
import { ECultureDateTypes, EDateFormatStyles } from "app/common/models/ECultureFormats";
import { Store } from "@ngrx/store";


enum EControlDataSource {
  Fixed,
  Dynamic,
  Context
};

export abstract class BaseDataControlComponent extends BaseControlComponent {

  private _data: any[] = [];
  private _fields: { [key: string]: any } = {};
  private _userPreferences: IUserPreferences;
  private _currentUserPreferences: IUserPreferences;
  protected actions: Array<IBizagiAction>;
  protected bizagiEntity: string;
  protected bizagiDataServices: IBizagiDataServices;
  protected pageRenderingService: PageRenderingService;
  protected translateService: TranslateService;
  protected route: ActivatedRoute;
  protected router: Router;
  protected xpathFilter = "";
  protected maxElementHeight = -1;

  public isDataReady = true;
  public isDataError = false;
  public canShowCaseLink = false;
  public pagination: IPagination = {
    currentPage: 0,
    currentPaginationSet: 0,
    from: 0,
    intervalSize: 3,
    pages: [0, 1, 2],
    rows: 3,
    totalPages: 3,
    totalItems: 0
  };

  public get data() {
    return this._data;
  }

  public get fields() {
    return this._fields;
  }

  public setServices(route: ActivatedRoute,
    router: Router,
    translateService: TranslateService,
    services: IBizagiDataServices,
    pageRenderingService?: PageRenderingService) {
    this.route = route;
    this.router = router;
    this.translateService = translateService;
    this.bizagiDataServices = services;
    this.pageRenderingService = pageRenderingService;
  }

  public initialized() {
    // This method is called after all the controls in the page are fully initialized, and the page mappings are applied
    this._loadData();
  }

  public refresh(isLastActionNext?: boolean) {
    if (isLastActionNext) {
      let totalItems = this.pagination.totalItems - 1;
      if (this.pagination.from === totalItems) {
        this.pagination.from = this.pagination.from - this.pagination.rows;
        this.pagination.currentPage--;
      }
    }
    super.refresh();
    this._loadData();
  }

  public setXpathFilter(xpathFilter: string) {
    this.xpathFilter = xpathFilter;
    this.pagination.currentPage = 0;
    this.pagination.from = 0;
  }

  public createCase(idAction, idProcess: string, displayName: string, key: string) {
    if (this.isRenderingMode()) {
      let url = this.contextServices.getBizagiProjectUrl(this.parameters["data"].bizagi.projectId);
      this.pageRenderingService.showForm({
        connection: url,
        title: displayName,
        type: FormType.New,
        parameters: {
          action: idAction,
          process: idProcess,
          bizagiEntity: this.bizagiEntity,
          key: key
        },
        refresh: this.refresh.bind(this)
      });
    }
  }

  public goToLink(item) {
    if (this.isRenderingMode() && this.parameters.data.dataSource === EControlDataSource.Fixed && item._link && item._link.type !== "none") {
      if (item._link.type === "external") {
        item._link.url = !/^(http:|https:)/i.test(item._link.url) ? "//" + item._link.url : item._link.url;
        window.open(item._link.url, item._link.blank ? "_blank" : "_self");
      } else {
        let site = this.contextServices.getSite();
        if (item._link.blank) {
          let currentPage = this.contextServices.getPage();
          window.open(location.href.replace(site.url + "/" + currentPage.name, site.url + "/" + item._link.pageName), item._link.blank ? "_blank" : "_self");
        } else {
          this.router.navigate([site.url, item._link.pageName]);
        }
      }
    }
  }

  public hasLink(item) {
    return this.isRenderingMode() &&
      ((item._link && item._link.type !== "none" && ((item._link.url && !/^\s+$/.test(item._link.url)) ||
        item._link.pageName)) ||
        (this.parameters.data && this.parameters.data.bizagi && this.parameters.data.bizagi.navigation &&
          JSON.stringify(this.parameters.data.bizagi.navigation).match("page")));
  }

  public setPagination(pagination: IPagination) {
    this.pagination = pagination;
    this._loadData();
  }

  public scrollToElement(element: HTMLElement) {
    element.scrollIntoView({ behavior: "smooth", block: "nearest" });
  }

  public async showCase(idCase: string, idTask?: string, processName?: string) {
    if (this.isRenderingMode()) {
      let url: string;
      if (this.parameters["data"].bizagi.type === "inbox") {
        let site = this.contextServices.getSite();
        url = (<any>_.first(_.values(site.environments[site.defaultEnvironment].projects))).url;
      } else {
        url = this.contextServices.getBizagiProjectUrl(this.parameters["data"].bizagi.projectId);
      }
      let displayName = await this.translateService.get("sitemanager.global.VIEW_CASE_TITLE", { case: idCase }).toPromise();
      let parameters: any = {};
      parameters.case = idCase;
      parameters.processId = this.parameters["data"].bizagi && this.parameters["data"].bizagi.dataId ? this.parameters["data"].bizagi.dataId : undefined;
      if (idTask) {
        parameters.idTask = idTask;
      }
      this.pageRenderingService.showForm({
        connection: url,
        title: processName || displayName,
        type: FormType.Existing,
        parameters: parameters,
        refresh: this.refresh.bind(this)
      });
    }
  }

  public refreshFixedPaginationData() {
    this.pagination.pages = this._refreshFixedPages(this._data);
    this.pagination.totalPages = this.pagination.pages.length;
    this._data = this._data.slice(this.pagination.from, this.pagination.from + this.pagination.rows);
  }

  public getTypeMappedField(field: string) {
    return this.control.parameters.data.bizagi && this.control.parameters.data.bizagi.mappingTypes
      ? this.control.parameters.data.bizagi.mappingTypes[field] : "";
  }

  protected onChanged(updatedControl: IControl) {
    super.onChanged(updatedControl);
    this._loadData();
  }

  protected navigate(item: any, section: string) {
    if (this.isRenderingMode()) {
      let data = this.parameters["data"];
      if (!data.bizagi) {
        return;
      }
      if (!data.bizagi.navigation) {
        return;
      }
      if (!data.bizagi.navigation[section]) {
        return;
      }
      if (!data.bizagi.navigation[section].page) {
        return;
      }

      // Perform routing
      let site = this.contextServices.getSite();
      let page = this.contextServices.getPage();
      let pageName = (<IBasicPage>_.find(this.contextServices.getPages(), { originalId: data.bizagi.navigation[section].page })).name;
      let key = data.bizagi.type === "cases" && item.caseNumber ? item.caseNumber : item.id;
      let navigationContextList: Array<INavigationContext> = page.isContextualized ?
        this.contextServices.getNavigationContext() : [];
      let newNavigationContext: INavigationContext = _.last(data.bizagi.navigation[section].navigationData);
      newNavigationContext.key = key;
      newNavigationContext.originPageId = this.contextServices.getPage().originalId;
      navigationContextList.push(newNavigationContext);
      this.router.navigate([site.url, pageName], {
        queryParams: {
          "key": key, "project": data.bizagi.projectId,
          "context": Global.jsonToBase64(navigationContextList)
        }
      });
    }
  }

  protected resize(event: any) {
    if (
      event.target.height > this.maxElementHeight &&
      this.controlDefinition.defaultSize.dynamic &&
      this.controlDefinition.styles.layout &&
      this.styles.layout &&
      this.styles.multipageItemView !== "Scroll") {
      this.maxElementHeight = event.target.height;
      let columns = this.controlDefinition.styles.layout.layouts[this.styles.layout].columns || 1;
      let rows = Math.ceil(this.pagination.rows / columns);
      let verticalSpacing = (this.controlDefinition.styles.layout.layouts[this.styles.layout].verticalItemSpacing || 3) * rows;
      let paginationsHeight = this.styles.multipageItemView === "Pagination" ? 40 : 10;
      let itemHeight = $("[data-control-id=" + this.control.id + "] [bs-dynamic-size-item=true]").outerHeight(true);
      let dymanicSizeElement = $("[data-control-id=" + this.control.id + "]");
      let heightElement = (rows * (itemHeight + verticalSpacing)) + paginationsHeight;
      if (dymanicSizeElement && heightElement) {
        this.onResize.next({
          width: dymanicSizeElement.width(),
          height: heightElement
        });
      }
    }
  }

  private async loadBizagiCollectionsData(data, filter) {
    // BIZAGI DATA SOURCE MANAGEMENT
    let parameters: ICollectionDataParams = {
      collectionId: data.bizagi.dataId,
      value: this._currentLanguageResources[filter] || filter,
      xpath: data.bizagi.mappings[this.xpathFilter],
      columns: <string[]>_.compact(_.uniq(_.values(data.bizagi.mappings))),
      pagination: this.pagination,
      mappingType: data.bizagi.mappingTypes[this.xpathFilter]
    };
    let url = this.contextServices.getBizagiProjectUrl(data.bizagi.projectId);
    let response = await this.bizagiDataServices.getCollection(url, parameters);
    if (response) {
      this.setResponseData(response);
    } else {
      this.isDataError = true;
    }
  }

  private async loadBizagiCasesData(data, filter) {
    // BIZAGI DATA SOURCE MANAGEMENT
    let parameters: ICaseDataParams = {
      processId: data.bizagi.dataId,
      value: this._currentLanguageResources[filter] || filter,
      xpath: data.bizagi.mappings[this.xpathFilter],
      columns: <string[]>_.compact(_.uniq(_.values(data.bizagi.mappings))),
      pagination: this.pagination,
      mappingType: data.bizagi.mappingTypes[this.xpathFilter]
    };

    let url = this.contextServices.getBizagiProjectUrl(data.bizagi.projectId);
    let response = await this.bizagiDataServices.getCases(url, parameters);
    if (response) {
      this.setResponseData(response);
    } else {
      this.isDataError = true;
    }
  }

  private _loadData() {
    this._fields = this._loadFields(this.control.fields);
    this.maxElementHeight = -1;
    let data = this.parameters["data"];
    this._updatePagination();
    let filter: string = Global.isOnRenderMode() && this.parameters["filter"] ? this.parameters["filter"] : "";
    filter = filter.trim().replace(/\*/g, "");
    let state: string = Global.isOnRenderMode() && this.parameters["state"] ? this.parameters["state"] : "";
    state = state.trim().replace(/\*/g, "");
    let orderby: string = Global.isOnRenderMode() && this.parameters["orderby"] ? this.parameters["orderby"] : "";
    orderby = orderby.trim().replace(/\*/g, "");

    if (data.dataSource === EControlDataSource.Dynamic && data.bizagi && data.bizagi.mappingTypes) {
      this.actions = data.bizagi.actions;
      this.canShowCaseLink = data.bizagi.type === "cases";
      if (!Global.isOnRenderMode()) {
        // BUILDING MODE DUMMIES
        if (_.isEmpty(this.controlDefinition.fields)) {
          this._data = DefaultValues.loadDefaultData(this.controlDefinition.parameters["data"].mappings);
        } else {
          let fieldsMapping = [];
          _.each(this.controlDefinition.fields, (controlDefinitionField: IControlDefinitionField, controlDefinitionFieldKey: string) => {
            fieldsMapping[controlDefinitionFieldKey] = {
              label: controlDefinitionField.displayName, type: controlDefinitionField.type,
              typeData: data.bizagi.mappingTypes[controlDefinitionFieldKey]
            };
          });
          this._data = DefaultValues.loadDefaultData(fieldsMapping);
        }
        return;
      }

      this.isDataReady = false;
      switch (data.bizagi.type) {
        case "cases": {
          // DATA FROM BIZAGI CASES
          this.loadBizagiCasesData(data, filter);
          break;
        }
        case "collections": {
          // DATA FROM BIZAGI COLLECTIONS
          this.loadBizagiCollectionsData(data, filter);
          break;
        }
        case "searches": {
          // DATA FROM BIZAGI CASES
          this.loadBizagiSearchResultsData(data, filter);
          break;
        }
        case "context": {
          // DATA FROM CONTEXTUALIZED DATA
          this.loadBizagiContextualizedResultsData(data, filter);
          break;
        }
        case "inbox": {
          this.loadInboxResultsData(data, filter, state, orderby);
          break;
        }

      }
    } else if (data.fixed) {
      // FIXED DATA MANAGEMENT
     _.each(data.fixed, (item, i) => {
        _.each(item, (value, key) => {
          this._data[i] = this._data[i] ? this._data[i] : {};
          this._data[i][key.toString()] = this._currentLanguageResources[value] ? this._currentLanguageResources[value] : value;
        });
      });
      if (filter) {
        this._data = _.filter(this.data, (item: any) => this.filterFixedItem(item, filter));
      }
      if (this.controlDefinition.parameters.data.type === "data") {
        this.refreshFixedPaginationData();
      }
    }
  }

  private async loadBizagiSearchResultsData(data, filter) {
    // BIZAGI DATA SOURCE MANAGEMENT
    let parameters: ISearchDataParams = {
      searchId: data.bizagi.dataId,
      value: this._currentLanguageResources[filter] || filter,
      xpath: data.bizagi.mappings[this.xpathFilter],
      pagination: this.pagination,
      mappingType: data.bizagi.mappingTypes[this.xpathFilter]
    };

    let url = this.contextServices.getBizagiProjectUrl(data.bizagi.projectId);
    let response = await this.bizagiDataServices.getSearchResults(url, parameters);
    if (response) {
      this.setResponseData(response);
    } else {
      this.isDataError = true;
    }
  }

  private async loadBizagiContextualizedResultsData(data, filter) {

    let navigationContexts = this.contextServices.getNavigationContext();
    let navigationContext = <INavigationContext>_.first(navigationContexts);
    // Create a navigation context part
    let parameters: INavigationContextForListResult = {
      connectionEntityId: navigationContext.connectionId,
      rootConnectionType: navigationContext.connectionType,
      value: this._currentLanguageResources[filter] || filter,
      xpath: data.bizagi.mappings[this.xpathFilter],
      pagination: this.pagination,
      navigationLevels: [],
      key: navigationContext.key
    };
    let url = this.contextServices.getBizagiProjectUrl(data.bizagi.projectId);
    let page = this.contextServices.getPage();

    _.each(navigationContexts, (navigationContextItem: INavigationContext) => {
      if (navigationContextItem.navigationId && parameters.connectionEntityId !== navigationContextItem.navigationId) {
        parameters.navigationLevels.push({ navigationId: navigationContextItem.navigationId, key: page.context.entity.value || navigationContextItem.key });
      }
    });
    parameters.navigationLevels.push({ navigationId: data.bizagi.dataId });

    let response = await this.bizagiDataServices.getContextNavigations(url, parameters, this.pagination);
    if (response) {
      this.setResponseData(response);
    } else {
      this.isDataError = true;
    }
  }

  private async loadInboxResultsData(data, filter, state, orderby) {

    // Create a navigation context part
    let parameters = {
      filter: filter || undefined,
      state: state || undefined,
      orderby: orderby || undefined,
      pagination: this.pagination,
    };
    let site = this.contextServices.getSite();
    let url = (<any>_.first(_.values(site.environments[site.defaultEnvironment].projects))).url;
    let response = await this.bizagiDataServices.getInbox(url, parameters, this.pagination);
    if (response) {
      this.setResponseData(response, "Inbox");
    } else {
      this.isDataError = true;
    }
  }



  private setResponseData(response: IBizagiDataResult, dataType?: string) {
    this.cultureService.getPreferences().subscribe((userPreferences: IUserPreferences) => {
      this._userPreferences = userPreferences;
      this._currentUserPreferences = this._userPreferences.isCustom
        ? this._userPreferences
        : this._defaultPreferences;
      switch (dataType) {
        case "Inbox":
          this._setResponseDataInbox(response);
          break;
        default:
          this._setResponseData(response);
          break;
      }
    });
  }

  private _setResponseData(response: IBizagiDataResult) {
    this.pagination.totalItems = response.count;
    this.pagination.totalPages = response.count ? _.ceil(response.count / this.pagination.rows) : 0;
    this.pagination.pages = this.pagination.totalPages < this.pagination.intervalSize ?
      Array.from(Array(this.pagination.totalPages).keys()) : Array.from(Array(this.pagination.intervalSize).keys());
    this.bizagiEntity = response.bizagiEntity;
    this._data = this.transformData(response.instances);
  }

  private _setResponseDataInbox(response: IBizagiDataResult) {
    this.pagination.totalItems = response.count;
    this.pagination.totalPages = response.count ? _.ceil(response.count / this.pagination.rows) : 0;
    this.pagination.pages = this.pagination.totalPages < this.pagination.intervalSize ?
      Array.from(Array(this.pagination.totalPages).keys()) : Array.from(Array(this.pagination.intervalSize).keys());
    this.bizagiEntity = response.bizagiEntity;
    this._data = response.instances;
    _.each(this._data, (inboxCase) => {
      inboxCase.creationDate = this.cultureService.dateTimeFormat(
        inboxCase.creationDate,
        ECultureDateTypes.dateTime,
        this._currentUserPreferences);
      inboxCase.estimatedSolutionDate = this.cultureService.dateTimeFormat(
        inboxCase.estimatedSolutionDate,
        ECultureDateTypes.dateTime,
        this._currentUserPreferences);
      _.each(inboxCase.workitems, (workitem) => {
        workitem.estimatedSolutionDate = this.cultureService.dateTimeFormat(
          inboxCase.estimatedSolutionDate,
          ECultureDateTypes.dateTime,
          this._currentUserPreferences);
      })
    })
    this.isDataReady = true;
    if (!this.cdr.destroyed) {
      this.cdr.detectChanges();
    }
  }


  private filterFixedItem(item, filter) {
    let mappings = this.controlDefinition.fields;
    for (let key in mappings) {
      if (mappings[key]) {
        let mapping = mappings[key];
        if (mapping.type !== "image") {
          let fieldValue =  this._currentLanguageResources[item[key]] ? this._currentLanguageResources[item[key]] : item[key];
          let isAMatch = true;
          try {
            isAMatch = fieldValue.toLowerCase().match(filter.toLowerCase());
          } catch (e) { }
          if (isAMatch) {
            return true;
          }
        }
      }
    }
    return false;
  }

  private transformData(data) {
    let mappings = this.parameters["data"].bizagi.mappings;
    let mappingTypes = this.parameters["data"].bizagi.mappingTypes ? this.parameters["data"].bizagi.mappingTypes : {};
    let result = [];
    let dateFormat = this._currentUserPreferences.dateFormatStyle === EDateFormatStyles.long
      ? this._currentUserPreferences.dateFormatLong
      : this._currentUserPreferences.dateFormatShort;
    data.forEach((row: IBizagiDataResultItem) => {
      let item = { id: row.id };
      for (let column in mappings) {
        if (mappings[column]) {
          let match = _.filter(row.parameters, { xpath: mappings[column] });
          if (match && match.length > 0) {
            let value;
            if ((<any>match[0]).hasOwnProperty("value")) {
              value = (<any>match[0]).value;
            } else {
              value = (<any>match[0]).files;
            }
            item[column] = value;
            let formatType = mappingTypes[column];
            let mappingMetadata = this.controlDefinition.fields ? this.controlDefinition.fields : (<any>this.controlDefinition.parameters).data.mappings;
            switch (formatType.toLowerCase()) {
              case "image":
                // When the data is an image, add the prefix for image64
                if (mappingMetadata[column].type.toLowerCase && mappingMetadata[column].type.toLowerCase() === "image" && Global.isBizagiImage(value)) {
                  item[column] = value ? Global.getBizagiImageUrl(value) : null;
                }
                break;
              case "datetime":
                item[column] = this.cultureService.dateTimeFormat(value, ECultureDateTypes.date, this._currentUserPreferences);
                break;
              case "money":
                item[column] = this.cultureService.currencyFormat(value, this._currentUserPreferences);
                break;

              case "float":
                if (value && this.cultureService.countDecimals(value) > 2) {
                  item[column] = parseFloat(value).toFixed(2);
                }
                break;
            }
          }
        }
      }
      if (row.actions) {
        item["actions"] = row.actions;
      }
      if (this.canShowCaseLink) {
        item["idCase"] = row["idCase"] || row["id"];
        item["caseNumber"] = row["caseNumber"];
        item["id"] = row["@processEntityKey"];
      }
      result.push(item);
    });
    this.isDataReady = true;
    return result;
  }

  private isText(column, item) {
    if (item === null) {
      return false;
    }
    let mappingMetadata = (<any>this.controlDefinition.parameters).data.mappings;
    return mappingMetadata[column].type.toLowerCase() === "text";
  }

  private _updatePagination() {
    this.pagination.rows = this.styles.displayedItems || 5;
  }

  private _refreshFixedPages(data: any) {
    let pages: any[] = [];
    for (let i = 0; i < Math.ceil(data.length / this.pagination.rows); i++) {
      pages.push(i);
    }
    return pages;
  }

  private _loadFields(fields: { [key: string]: any }): { [key: string]: any } {
    let translationFields: { [key: string]: any } = {};
    _.each(fields, (field: any, key: string) => {
      let tmpField = _.cloneDeep(field);
      if (tmpField.styles["fieldLabel"]) {
        tmpField.styles["fieldLabel"].text = this._currentLanguageResources[field.styles["fieldLabel"].text];
      }
      translationFields[key] = tmpField;
    });
    return translationFields;
  }
}
