import { Injectable } from "@angular/core";

@Injectable()
export class ThemeService {
  private themes: Array<ITheme>;
  private webpartThemes: any;
  private themeVariables: { [key: string]: string } = {};

  constructor() {
    this.themes = [
      {
        id: "6b72f0d0-c6b8-4bc9-8506-b20982643a00",
        displayName: "Luxury",
        colors: ["#89711f", "#c0b283", "#dcd0c0", "#f4f4f4", "#f0ebe7", "#373737"]
      },
      {
        id: "0748c562-1d16-439e-8795-8f0be0bb5971",
        displayName: "Sky",
        colors: ["#4484ce", "#f2bf08", "#00142b", "#f4f4f4", "#e7e7e7", "#373737"]
      },
      {
        id: "3ed7d38a-af57-429f-b829-e4de141fbc16",
        displayName: "Modern",
        colors: ["#07889b", "#adc345", "#033840", "#ffffff", "#f4f4f4", "#373737"]
      },
      {
        id: "a13d181a-5e75-4055-8569-56f0c92bb2df",
        displayName: "Beezagi",
        colors: ["#0a1612", "#f7ce3e", "#1a2930", "#ffffff", "#dddee2", "#373737"]
      },
      {
        id: "bfef2652-eac3-4bf6-b30d-7094de2cad00",
        displayName: "Clear",
        colors: ["#033840", "#94c2cc", "#caebf2", "#ffffff", "#efefef", "#373737"]
      },
      {
        id: "4760dc64-e96b-426b-b60c-a04f47f2b4ac",
        displayName: "Lavender",
        colors: ["#513746", "#bbb5c1", "#846d7a", "#ffffff", "#f4f4f4", "#373737"]
      }
    ];
    this.webpartThemes = {
      "active-border-radius": "true",
      "active-box-shadow": "false",
      "active-smart-contrast": "false",
      "active-text-shadow": "false",
      "background": "color-fourth",
      "button": "color-second",
      "button-text": "color-sixth",
      "content": "color-fourth",
      "content-border": "color-fourth",
      "content-link": "color-sixth",
      "font-family": "'Open Sans', 'Helvetica Neue', Arial, Helvetica, sans-serif",
      "form-container": "color-fifth",
      "form-grid": "color-third",
      "form-grid-border": "color-second",
      "form-group": "color-fifth",
      "form-group-border": "color-second",
      "form-input": "color-fourth",
      "form-input-border": "color-second",
      "form-tab": "color-third",
      "form-tab-border": "color-second",
      "form-text": "color-sixth",
      "logo-background": "color-fourth",
      "minimum-line-height": "inactive",
      "modal": "color-fifth",
      "modal-border": "color-second",
      "navigation": "color-second",
      "navigation-border": "color-fifth",
      "navigation-link": "color-second",
      "navigation-submenu-background": "color-fifth",
      "navigation-submenu-text": "color-sixth",
      "normal-line-height": "active",
      "pane": "color-third",
      "sidebar-image-theme": "url('jquery/workportal/css/desktop/images/bg-landscape.png')",
      "text": "color-sixth",
      "title": "color-first",
      "tooltip": "color-sixth",
      "sites": "true"
    };
  }
  public getThemes() {
    return this.themes;
  }

  public loadSiteTheme(theme: string) {
    let head = document.getElementsByTagName("head")[0];
    let link = document.createElement("link");
    link.rel = "stylesheet";
    link.type = "text/css";
    link.href = "assets/themes/" + theme + ".min.css";
    link.media = "all";
    head.appendChild(link);
  }

  public refreshThemeVariables() {
    let regexp = /#bs-form-theme-([\w-]+)__([\w-]+)/g;

    // First, iterate through all stylesheets
    for (let i = 0; i < document.styleSheets.length; i++) {
      try {
        let mysheet = <any>(document.styleSheets[i]);
        let myrules = mysheet.cssRules ? mysheet.cssRules : mysheet.rules || [];

        // Second, iterate through all rules in the given stylesheet
        for (let j = 0; j < myrules.length; j++) {
          let selector = myrules[j].selectorText;
          if (selector) {
            // Try to match, in order to find the different generated rules for form themes
            let match = regexp.exec(selector);
            while (match != null) {
              let variable = match[1];
              let cssProp = match[2];
              this.themeVariables[variable] = myrules[j].style[cssProp];

              // Get next match
              match = regexp.exec(selector);
            }
          }
        }
      } catch (error) {
       console.log("error to load themes", error);
      }
    }
  }
  public getThemeVariables() {
    return this.themeVariables;
  }
  public getWebpartThemeVariables(): any {
    let webpartMappedTheme: { [key: string]: string } = Object.assign({}, this.webpartThemes);
    for (const key of Object.keys(this.webpartThemes)) {
      if (this.themeVariables[this.webpartThemes[key]]) {
        webpartMappedTheme["@" + key] = this.themeVariables[this.webpartThemes[key]];
      } else {
        webpartMappedTheme["@" + key] = this.webpartThemes[key];
      }
      delete webpartMappedTheme[key];
    }
    return webpartMappedTheme;
  }
}
