import {
  CATEGORY,
  CIRCLE_CONTROLS,
  SUBCATEGORY_CIRCLE
} from "./../controls-name-definition";
import {Component} from "@angular/core";
import {BaseControlComponent} from "app/common/controls/base.component";
import {ControlDefinition} from "app/common/decorators/control-definition";

@Component({
  selector: "bs-circle-control",
  templateUrl: "./circle-control.component.html",
  styleUrls: ["./circle-control.component.scss"]
})
@ControlDefinition(
  {
    type: CIRCLE_CONTROLS.circle.type,
    category: CATEGORY.FIGURES.displayName,
    subcategory: SUBCATEGORY_CIRCLE.displayName,
    displayName: CIRCLE_CONTROLS.circle.displayName,
    icon: CIRCLE_CONTROLS.circle.icon,
    styles: {
      keepSize: {
        type: "boolean",
        defaultValue: false
      },
      opacity: {
        type: "range",
        defaultValue: 100,
        min: 0,
        max: 100
      },
      color: {
        type: "color",
        defaultValue: "none"
      },
      border: {
        type: "border",
        defaultValue: "1px solid #000"
      }
    },
    parameters: {
    },
    defaultSize: {
      width: 200,
      height: 204
    },
    hiddenOptions: {
      dataManager: true
    }
  }
)

export class CircleControlComponent extends BaseControlComponent {

  fontWeight() {
    let bold = this.styles["bold"];
    return bold === true ? "600" : "100";

  }
}
