import {Component} from "@angular/core";
import {ControlDefinition} from "app/common/decorators/control-definition";
import {CATEGORY, SUBCATEGORY_MY_RELEVANTS} from "./../controls-name-definition";
import {BaseProcessControlComponent} from "app/common/controls/base-process.component";
import {DefaultValues} from "app/common/misc/default-values";
import {TranslateService} from "@ngx-translate/core";

@Component({
  selector: "bs-my-relevants-control",
  templateUrl: "./my-relevants-control.component.html",
  styleUrls: ["./my-relevants-control.component.scss"]
})
@ControlDefinition({
  type: "my-relevants",
  displayName: "MY_RELEVANTS_CONTROL",
  category: CATEGORY.EXPERIENCE_ELEMENTS.displayName,
  subcategory: SUBCATEGORY_MY_RELEVANTS.displayName,
  icon: SUBCATEGORY_MY_RELEVANTS.controls.MyRelevants.icon,
  styles: {
    layout: {
      type: "layout",
      defaultValue: "free",
      category: "hidden",
      layouts: {
        "free": {
          icon: "free-menu",
          text: "FREE_MENU",
          columns: 3
        },
        "small": {
          icon: "small-menu",
          text: "SMALL_MENU",
          columns: 1
        },
        "medium": {
          icon: "medium-menu",
          text: "MEDIUM_MENU",
          min: 50,
          columns: 2
        },
        "large": {
          icon: "large-menu",
          text: "LARGE_MENU",
          min: 80,
          columns: 3
        }
      }
    },
    align: {
      type: "text-align",
      defaultValue: "left"
    },
    fontFamily: {
      type: "font-family",
      defaultValue: "Open Sans"
    },
    titleTextColor: {
      type: "color",
      defaultValue: "#FFFFFF"
    },
    titleBackColor: {
      type: "color",
      defaultValue: "#364855"
    },
    buttonsFillContent: {
      type: "fill-content",
      defaultValue: "filled"
    },
    showButtonIcon: {
      type: "boolean",
      defaultValue: true
    },
    buttonsFontColor: {
      type: "color",
      defaultValue: "#364855"
    },
    buttonsBackColor: {
      type: "color",
      defaultValue: "#FFFFFF"
    }
  },
  parameters: {
    menuName: {
      type: "title"
    }
  },
  defaultSize: {
    width: 300,
    height: 530
  },
})

export class MyRelevantsControlComponent extends BaseProcessControlComponent {
  public expanded = {};

  constructor(private _translateParser: TranslateService) {
    super();
  }

  async ngOnInit() {
    if (this.isRenderingMode()) {
      this.data =  await this.loadRelevants()
    } else {
      this.data = await DefaultValues.getDefaultProcesess(this._translateParser);
    }
  }

  public toggle(relevantCategory: string) {
    this.expanded[relevantCategory] = this.expanded[relevantCategory] ? false : true;
  }
}
