  public static getApplicationPath() {
    return (window.location.origin ? window.location.origin : (window.location.protocol + "//"
      + window.location.hostname)) + window.location.pathname.substring(0, window.location.pathname.lastIndexOf("/"))
      + "/";
  }

  public static getApplicationName() {
    return (window.location.pathname.indexOf("render.html") >= 0) ? "render" : "builder";
  }


  public static getScreenSize(): string {
    if (window.matchMedia("(max-width: 480px)").matches) {
      return "mobile";
    } else if (window.matchMedia("(min-width: 480px) and (max-width: 768px)").matches) {
      return "tablet";
    } else if (window.matchMedia("(min-width: 768px) and (max-width: 1200px)").matches) {
      return "desktop";
    } else if (window.matchMedia("(min-width: 1201px)").matches) {
      return "largedesktop";
    }
  }

  public static getControlsByStakeholderContext(stakeholderContextId: string | Array<string>, controls: Array<IControl>): Array<IControl> {
    let filteredControls: Array<IControl> = [];
    let copy = _.cloneDeep(controls);
    _.forEach(copy, (control: IControl) => {
      if (!this.isVisibleTo(control.visibleTo, stakeholderContextId)) {
        this.updateStakeholderViewPosition(copy, control.location);
      } else {
        filteredControls.push(control);
      }
    });
    return filteredControls;
  }

  public static cleanPageStructure(page: IPage): IPage {
    let cleanedPage = _.cloneDeep(page);
    cleanedPage.hero = this._cloneControl(cleanedPage.hero);
    for (let i in cleanedPage.controlGroups.byGroups) {
      if (cleanedPage.controlGroups.byGroups[i]) {
        // delete cleanedPage.controlGroups.byGroups[i].selectedControls;
      }
    }
    for (let i in cleanedPage.controls) {
      if (cleanedPage.controls[i]) {
        cleanedPage.controls[i] = this._cloneControl(cleanedPage.controls[i]);
      }
    }
    return cleanedPage;
  }

  private static _cloneControl(control): IControl {
    let newControl = <IControl>_.omit(control, ["changed"]);
    return newControl;
  }

  public static cleanSiteStructure(site: ISite) {
    let cleanedSite = _.omit(site, ["header.changed"]);
    cleanedSite = _.omit(cleanedSite, ["footer.changed"]);
    return cleanedSite;
  }


  public static getBizagiImageUrl(imageUrl: string) {
    let urlParts = imageUrl.split("/odata/image?");
    let code = urlParts[1].split("code=")[1];
    let url = urlParts[0];
    let basePath = this.getApplicationPath();
    if (!basePath.endsWith("/")) {
      basePath += "/";
    }
    return `${basePath}api/bizagi/image?url=${url}&code=${code}`;
  }

  public static isBizagiImage(image) {
    if (image === null) {
      return false;
    }
    return (image.startsWith("http://") || image.startsWith("https://")) && _.includes(image, "odata/image");
  }

  public static getQueryString() {
    if (window.location.search && window.location.search.length > 0) {
      return this.parseQueryString(window.location.search.substr(1));
    }
    if (window.location.hash.indexOf("#") === 0) {
      let url = window.location.hash;
      if (url.indexOf("?") < 0) {
        return {};
      }
      let queryString = url.substring(url.indexOf("?") + 1);
      return this.parseQueryString(queryString);
    } else {
      return {};
    }
  };

  public static replaceAll(text: string, textToReplace: string, replacement: string) {
    return text.replace(new RegExp(textToReplace, "g"), replacement);
  }

  private static parseQueryString(queryString) {
    let data = {}, pairs, pair, separatorIndex, escapedKey, escapedValue, key, value;
    if (queryString === null) {
      return data;
    }
    pairs = queryString.split("&");

    for (let i = 0; i < pairs.length; i++) {
      pair = pairs[i];
      separatorIndex = pair.indexOf("=");

      if (separatorIndex === -1) {
        escapedKey = pair;
        escapedValue = null;
      } else {
        escapedKey = pair.substr(0, separatorIndex);
        escapedValue = pair.substr(separatorIndex + 1);
      }

      key = decodeURIComponent(escapedKey);
      value = decodeURIComponent(escapedValue);

      if (key.substr(0, 1) === "/") {
        key = key.substr(1);
      }

      data[key] = value;
    }
    return data;
  };

  public static isVisibleTo(visibleToList: Array<string>, stakeholderContextId: string | Array<string>): boolean {
    if (_.isEmpty(visibleToList)) {
      return false;
    }
    if (JSON.stringify(visibleToList).match("ALL")) {
      return true;
    }
    let canShowControl = false;
    if (stakeholderContextId instanceof Array) {
      _.each(stakeholderContextId, (id) => {
        if (JSON.stringify(visibleToList).match(id)) {
          canShowControl = true;
        }
      });
    } else {
      canShowControl = JSON.stringify(visibleToList).match(stakeholderContextId) !== null;
    }
    return canShowControl;
  }

  private static updateStakeholderViewPosition(controls: Array<IControl>, controlPosition: ILocation) {
    _.forEach(controls, (control: IControl) => {
      if (control.location.y > controlPosition.y) {
        control.location.y = control.location.y - controlPosition.height;
      }
    });
  }

  public static capitalizeFirstLetter(text: string) {
    let textLower = text.toLowerCase();
    return textLower.toLowerCase().charAt(0).toUpperCase() + textLower.slice(1);
  }

  public static copyToClipBoard(text: string): boolean {
    let id = "mycustom-clipboard-textarea-hidden-id";
    let existsTextarea: any = document.getElementById(id);

    if (!existsTextarea) {
      let textarea = document.createElement("textarea");
      textarea.id = id;
      // Place in top-left corner of screen regardless of scroll position.
      textarea.style.position = "fixed";
      textarea.style.top = "0";
      textarea.style.left = "0";
      textarea.style.width = "1px";
      textarea.style.height = "1px";
      textarea.style.padding = "0";
      textarea.style.border = "none";
      textarea.style.outline = "none";
      textarea.style.boxShadow = "none";
      textarea.style.background = "transparent";
      document.querySelector("body").appendChild(textarea);
      existsTextarea = document.getElementById(id);
    }
    existsTextarea.value = text;
    existsTextarea.select();

    let status = document.execCommand("copy");
    return status;
  }

  public static sortByDefinition(definition, items, sortKey) {
    let result = [];
    definition.forEach(function (key) {
      let found = false;
      items = items.filter(function (item) {
        if (!found && item[sortKey] === key) {
          result.push(item);
          found = true;
          return false;
        } else {
          return true;
        }
      })
    })

    return result;
  }
