import {Component} from "@angular/core";
import {Router} from "@angular/router";

@Component({
  selector: "bs-site-list-render",
  templateUrl: "./site-list-render.component.html",
  styleUrls: ["./site-list-render.component.scss"]
})
export class SiteListRenderComponent {

  public publishedSites: Array<any> = [];
  public isLoading: boolean;


  constructor(private _router: Router) {
  }

  async ngOnInit() {
    this._router.navigate(["404"]);
  }


  public goToSite(publishedSite: any) {
    this._router.navigate(["", publishedSite.name]);
  }
}
