import { Component, ViewChild, ElementRef } from "@angular/core";
import { BaseControlComponent } from "app/common/controls/base.component";
import { ControlDefinition } from "app/common/decorators/control-definition";
import { CATEGORY, SUBCATEGORY_SEARCH, SEARCH_CONTROLS } from "./../controls-name-definition";
import { ThemeService } from "app/common/services/sites/theme.service";

@Component({
  selector: "bs-search-control",
  templateUrl: "./search-control.component.html",
  styleUrls: ["./search-control.component.scss"]
})
@ControlDefinition(
  {
    type: SEARCH_CONTROLS.search.type,
    category: CATEGORY.BUNDLES.displayName,
    subcategory: SUBCATEGORY_SEARCH.displayName,
    displayName: SEARCH_CONTROLS.search.displayName,
    icon: SEARCH_CONTROLS.search.icon,
    styles: {
      displaySearch: {
        type: "display-search",
        defaultValue: "boxed"
      },
      backColor: {
        type: "color",
        defaultValue: "color-sixth"
      },
      buttonBackColor: {
        type: "color",
        defaultValue: "color-second"
      },
      buttonFontColor: {
        type: "color",
        defaultValue: "color-sixth"
      },
      iconColor: {
        type: "color",
        defaultValue: "color-sixth"
      },
      border: {
        type: "border",
        defaultValue: "1px solid #000"
      }
    },
    parameters: {
      searchButtonName: {
        type: "title",
        defaultValue: "Search"
      },
      searchPlaceholder: {
        type: "title",
        defaultValue: "Type something"
      },
      searchIcon: {
        type: "icon-selector",
        defaultValue: "search"
      }
    },
    defaultSize: {
      width: 800,
      height: 125,
      minWidth: 492
    },
    actions: {
      onSearch: {
        outputs: [
          {
            type: "text",
            name: "search"
          }
        ]
      }
    },
    hiddenOptions: {
      dataManager: true
    }

  }
)
export class SearchControlComponent extends BaseControlComponent {

  public textsearch = "";
  public themeVariables: Object;
  @ViewChild("TextSearch", { static: false }) searchInput: ElementRef;

  constructor(private _themeServices: ThemeService) {
    super();
  }

  public onSearchHandler() {
    this.notifyChange({
      action: "onSearch",
      value: {
        search: this.searchInput.nativeElement.value
      }
    });
  }

  ngOnInit() {
    this._themeServices.refreshThemeVariables();
    this.themeVariables = this._themeServices.getThemeVariables();
  }

  public getBackgroundColor() {
    if (this.styles["displaySearch"] === "boxed") {
      return this.themeVariables && this.themeVariables[this.styles["backColor"]] ? this.themeVariables[this.styles["backColor"]] : this.styles["backColor"];
    } else {
      return "transparent";
    }
  }
}
