import { Global } from "app/common/misc/global";

declare let bz: any;

export class OAuthClientHelper {

  protected oauth: any = new bz.oAuthClient();
  protected token: string;
  protected loggedOn: boolean;
  protected type: string; // accounts | bizagi
  protected sso: boolean;

  constructor(type: string, sso: boolean = false) {
    this.type = type;
    this.sso = sso;
  }

  public login(params: any, type: string) {
    let that = this;
    this.type = type;
    // Add type
    params = params || {};
    params.type = this.type;
    params.sso = this.sso;

    return new Promise((resolve, reject) => {
      that.oauth.initAndLogin(params).done(function () {
        that.token = that.oauth.getAccessToken();
        that.loggedOn = true;
        resolve();
      }).fail(function () {
        reject();
      });
    });
  }

  public logout() {
    this.oauth.logout(true);
  }

  public logoutOtherUrl(url: string) {
    this.oauth.logout(true, url);
  }

  public getToken(): string {
    return this.token;
  }

  public isLoggedOn(): boolean {
    return this.loggedOn && this.oauth.getProfile();
  }

  public getUserEmail(): string {
    let profile = this.getProfile();
    return profile.email;
  }

  public getUsername(): string {
    let profile = this.getProfile();
    return profile.username;
  }

  public getIsAdministrator(): boolean {
    let profile = this.getProfile();
    return profile.isAdministrator;
  }

  public hasEditorRole(): boolean {
    let profile = this.getProfile();
    return !!_.find(profile.roles, (role: { Name: string }) => {
      return role.Name === "BASiteEditor";
    });
  }

  public getUserId(): string {
    let profile = this.getProfile();
    return profile.sub;
  }

  public getProfile() {
    let profile = this.oauth.getProfile();
    return profile;
  }

  public refreshToken(): any {
    return this.oauth.refreshToken();
  }
}

// Bizagi oAuth client
export class BizagiOAuthClient extends OAuthClientHelper {
  constructor() { super("bizagi", true); }
}

// Accounts oAuth client
export class AccountsOAuthClient extends OAuthClientHelper {
  constructor() { super("accounts"); }
}

