import { Pipe, PipeTransform } from "@angular/core";

@Pipe({
  name: "bsObjectList"
})
export class ObjectListPipe implements PipeTransform {

  transform(value: any, args?: any[]): Object[] {
    let returnArray = [];
    let keys: Array<string> = Object.keys(value);

    keys.forEach((key: string) => {
      returnArray.push({
        key: key,
        val: value[key]
      });
    });
    return returnArray;
  }

}
