import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";

@Component({
    selector: "bs-inbox-control-card-view",
    templateUrl: "./inbox-card-view.component.html",
    styleUrls: ["./inbox-card-view.component.scss"]
})

export class InboxControlCardViewComponent {
    public activityExpandMode = { caseId: "" };
    @Input() pagination: IPagination;
    @Input() data: any;
    @Input() styles: any;
    @Input() control: any;
    @Input() isRenderingMode: boolean;
    @Input() dataSearchMode: string;

    public placeholderItems = [];
    @Output() showCaseEvent: EventEmitter<string[]> = new EventEmitter();

    constructor() { }

    public activityShowAll(caseNumber?: string) {
        if (this.activityExpandMode.caseId !== caseNumber) {
            this.activityExpandMode.caseId = caseNumber;
        }
    }

    public showCase(caseId: string, taskId?: string, processName?: string) {
        this.showCaseEvent.emit([caseId, taskId, processName]);
    }

    public getPlaceholderItems() {
        if (this.isRenderingMode && this.dataSearchMode !== "filter") {
          let placeholderSize = this.styles.displayedItems - this.data.length;
          if (placeholderSize > 0 && this.placeholderItems.length !== placeholderSize) {
            this.placeholderItems = new Array(placeholderSize).fill({
              processName: { type: "title", label: "DATA_PROPERTY_IMAGE" },
              caseNumber: { type: "bizagi-number", label: "DATA_PROPERTY_TITLE" },
              workitems: { type: "workitem", label: "DATA_PROPERTY_TEXT" },
              creationDate: { type: "date", label: "DATA_PROPERTY_TEXT" },
              solutionDate: { type: "date", label: "DATA_PROPERTY_TEXT" }
            });
          } else if (placeholderSize === 0) {
            this.placeholderItems = [];
          }
        } else {
          this.placeholderItems = [];
        }
        return this.placeholderItems;
      }
}
