
export class GlobalCompatibility {

    public static supportPageChanges(page: IPage): IPage {
        // Hero Image
        page = this.heroImageChanges(page);
        return page;
    }

    private static heroImageChanges(page: IPage) {

        if (page.hero.parameters && page.hero.parameters.heroImage) {
            page.hero.styles = {
                showHeroImage: page.hero.styles.showHeroImage,
                heroImageHeight: page.hero.styles.heroImageHeight || 550,
                heroSpace: "fluid",
                scale: "tofill",
                scaleProportionally: 50,
                imgOpacity: 100,
                imgColorOpacity: 0,
                imgColorBackgroundOpacity: "transparent",
                backgroundPosition: "center",
                heroImage: page.hero.parameters.heroImage

            };
            delete page.hero.parameters;
        }

        return page;
    }
}
