import {Component, Input} from "@angular/core";
import {BaseControlComponent} from "app/common/controls/base.component";
import {ControlDefinition} from "app/common/decorators/control-definition";
import {DefaultValues} from "../../misc/default-values";
import {Observable, Subscription} from "../../../../../node_modules/rxjs";
import {EHeroActions} from "../../../editor/models/EHeroActions";
import {EMenuOptions} from "app/editor/models/EMenuOption";
import {Store} from "@ngrx/store";

@Component({
  selector: "bs-hero-control",
  templateUrl: "./hero-control.component.html",
  styleUrls: ["./hero-control.component.scss"]
})
@ControlDefinition({
  type: "hero",
  displayName: "HERO_CONTROL",
  icon: "https://cdn2.iconfinder.com/data/icons/windows-8-metro-style/128/text.png",
  styles: {
    showHeroImage: {
      type: "boolean",
      defaultValue: true
    },
    heroImageHeight: {
      type: "range",
      defaultValue: 550,
      min: 300,
      max: 550
    },
    heroSpace: { type: "custom" },
    scale: { type: "custom" },
    scaleProportionally: { type: "custom" },
    imgOpacity: { type: "custom" },
    imgColorOpacity: { type: "custom" },
    imgColorBackgroundOpacity: { type: "custom" },
    backgroundPosition: { type: "custom" },
    heroImage: {
      type: "upload-image",
    }
  },
  parameters: {
  },
  navigation: []
})

export class HeroControlComponent extends BaseControlComponent {

  private heroUpdated$: Observable<IHeroAction>;
  private gridSize$: Observable<IGrid>;
  private currentMenu$: Observable<IControlMenuOption>;
  @Input() control: IControl;
  @Input() isRenderMode = false;
  public hero: IHeroExtendedAttributes;
  public grid: IGrid;
  public defaultImage = DefaultValues.getDefaultPicture();
  private _currentLanguageSubscription: Subscription;
  public $currentLanguage: Observable<any>;
  private _currentLanguage: ILanguageResource;

  constructor(private _store: Store<IEditorState>,
    private _renderStore: Store<IRenderState>) {
    super();
  }

  ngOnInit() {
    if (this.isRenderMode) {
      this.gridSize$ = this._renderStore.select((state: IRenderState) => state.grid);
      this.$currentLanguage = this._renderStore.select((state: IRenderState) => { return state.currentLanguage });
    } else {
      this.currentMenu$ = this._store.select((state: IEditorState) => state.siteEditor.currentMenu);
      this.heroUpdated$ = this._store.select((state: IEditorState) => state.siteEditor.heroAction);
      this.gridSize$ = this._store.select((state: IEditorState) => state.siteEditor.grid);
      this.$currentLanguage = this._store.select((state: IEditorState) => { return state.siteEditor.currentLanguage });
      this.heroUpdated$.subscribe((heroAction: IHeroAction) => {
        if (heroAction.action === EHeroActions.HeroUpdate) {
          this.hero = heroAction.hero;
        }
      });
    }
    this._currentLanguageSubscription = this.$currentLanguage.subscribe((currentLanguage: ILanguageResource) => {
      this._currentLanguage = currentLanguage;
    });

    this.gridSize$.subscribe((grid: IGrid) => {
      this.grid = grid;
    });
  }

  ngOnChanges() {
    if (this.control) {
      // This control is not loaded dinamically, so we need to initialize the control after the input is set
      this.initializeControl();
    }
  }

  ngOnDestroy() {
    this.hero = null;
    this.grid = null;
  }

  public getStylesImage() {
    let heroStyles = this.hero || this.styles;
    return {
      "background-image": this._currentLanguage && this._currentLanguage.resources && this._currentLanguage.resources[heroStyles.heroImage]
        ? "url(" + this._currentLanguage.resources[heroStyles.heroImage] + ")" : "url(" + heroStyles["heroImage"] + ")",
      "background-size": this.getBackgroundSize() || "cover",
      "background-repeat": this.getBackgroundRepeat() || "no-repeat",
      "opacity": heroStyles["imgOpacity"] / 100,
      "background-position": heroStyles["backgroundPosition"],
      "transform": this.getTransformScale() || "scale(1)",
      "zoom": this.getZoom() || "100%"
    }
  }

  public getStylesLayer() {
    let heroStyles = this.hero || this.styles;
    return {
      "opacity": heroStyles["imgColorOpacity"] / 100,
      "background-color": heroStyles["imgColorBackgroundOpacity"]
    }
  }

  public getBackgroundSize() {

    if (this.hero && this.hero.scale) {
      return this.calculateScale(this.hero.scale);
    }

    if (this.styles["scale"]) {
      return this.calculateScale(this.styles["scale"]);
    }

  }

  private getTransformScale() {
    let scaleProportionally;
    if (this.hero) {
      if (this.hero.scale !== "tile") {
        scaleProportionally = (this.hero.scaleProportionally / 100) + 0.5;
        return `scale(${scaleProportionally})`;
      }
    } else if (this.styles["scale"] && (this.styles["scale"] !== "tile")) {
      scaleProportionally = (this.styles["scaleProportionally"] / 100) + 0.5;
      return `scale(${scaleProportionally})`;
    }
  }

  private getZoom() {
    if ((this.hero && (this.hero.scale === "tile"))) {
      return `${this.hero.scaleProportionally + 50}%`;
    }
    if (this.styles["scale"] && (this.styles["scale"] === "tile")) {
      return `${this.styles["scaleProportionally"] + 50}%`;
    }
  }


  public getBackgroundRepeat() {
    if (this.hero && this.hero.scale) {
      return this.calculateBackgroundRepeat(this.hero.scale);
    }
    if (this.styles["scale"]) {
      return this.calculateBackgroundRepeat(this.styles["scale"]);
    }
  }

  public getWidthControl() {
    if (this.hero && this.hero.heroSpace === "container") {
      return this.grid.layoutSize + "px";
    } else if (this.hero && this.hero.heroSpace === "fluid") {
      return "100%";
    }

    if (this.styles["heroSpace"] === "container") {
      return this.grid.layoutSize + "px";
    } else {
      return "100%";
    }
  }

  public checkShowHero() {
    if (this.hero) {
      return this.hero.showHeroImage;
    }
    return this.styles["showHeroImage"] === true;
  }

  private calculateBackgroundRepeat(scale: string): string {
    switch (scale) {
      case "tile":
        return "repeat";

      default:
        return "no-repeat";
    }
  }

  private calculateScale(scale): string {
    switch (scale) {
      case "tofill":
        return "cover";

      case "original":
        return "auto";

      case "tile":
        return "initial";

      default:
        break;
    }
  }

}
