import * as MultilanguageActions from "./multilanguage.actions";


export function multilanguageReducer (state = null, action: MultilanguageActions.UpdateCurrentLanguage) {
  switch (action.type) {
    case MultilanguageActions.UPDATE_MULTILANGUAGE: {
      let currentLanguage = _.cloneDeep(action.payload);
      return {
        ...state,
        ...currentLanguage
      };
    }
    default:
      return state
  }
}
