import {from as observableFrom, throwError as observableThrowError} from "rxjs";

import {catchError, filter, finalize, switchMap, take} from "rxjs/operators";
import {Injectable} from "@angular/core";
import {HttpErrorResponse, HttpEvent, HttpHandler, HttpInterceptor, HttpRequest} from "@angular/common/http";
import {BehaviorSubject, Observable} from "rxjs/Rx";
import {OAuthClientHelper} from "app/common/services/oauth/oauth-client.helper";
import {NotificationService} from "app/common/services/bizagi/notification.service";
import {RestApiSevice} from "../../common/services/oauth/rest-api.service";

@Injectable()
export class RestApiRenderInterceptorService implements HttpInterceptor {
  isRefreshingToken = false;
  tokenSubject: BehaviorSubject<string> = new BehaviorSubject<string>(null);
  constructor(private _oauthClient: OAuthClientHelper,
    private _notificationService: NotificationService,
    private _restApiService: RestApiSevice) {
  }

  public intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    return next.handle(request).pipe(catchError(error => {
      if (error instanceof HttpErrorResponse) {
        switch ((<HttpErrorResponse>error).status) {
          case 401:
            return this.handle401Error(request, next);
          case 403:
              return this._handleError(error);
          default:
            return this._handleError(error);
        }
      }
    }));
  }

  private _handleError(error: Response | any) {
    let errMsg: string = error.message ? error.message : error.toString();
    this._notificationService.notify({ message: "messages.error.SERVER_ERROR", type: "error" });
    return observableThrowError(errMsg);
  }

  handle401Error(req: HttpRequest<any>, next: HttpHandler) {
    if (!this.isRefreshingToken) {
      this.isRefreshingToken = true;
      this.tokenSubject.next(null);
      let refreshPromise = this._oauthClient.refreshToken();
      if (refreshPromise) {
        let source = observableFrom(refreshPromise);

        return source.pipe(switchMap((newToken: any) => {
          if (newToken) {
            this.tokenSubject.next(newToken);
            this._restApiService.setAuthenticationToken(newToken.access_token);
            return next.handle(this._addToken(req, newToken.access_token));
          }
        }), catchError(error => {
          if (error && error.status === 401) {
            this._oauthClient.logout();
          }
          return observableThrowError(error);
        }), finalize(() => {
          this.isRefreshingToken = false;
        }));
      }

    } else {
      return this.tokenSubject.pipe(
        filter(token => token != null),
        take(1),
        switchMap(token => {
          return next.handle(req);
        }));
    }
  }

  private _addToken(req: HttpRequest<any>, token: string): HttpRequest<any> {
    return req.clone({ setHeaders: { Authorization: "Bearer " + token } })
  }

}
