import {Injectable} from "@angular/core";
import {Subject, Observable, Subscription} from "rxjs";
import {Global} from "../../misc/global";
import {ImagesService} from "../../../editor/services/images.service";
import {ContextService} from "app/common/services/sites/context.service";
import {Store} from "@ngrx/store";

declare let ImageCompressor;
let maxLenght = 300000;

@Injectable()
export class FileService {

      private _currentLanguageSubscription: Subscription;
  public $currentLanguage: Observable<ILanguageResource>;
  private _currentLanguage: ILanguageResource;

  constructor(private _imagesService: ImagesService, private _contextService: ContextService, private _store: Store<IEditorState>) {
    this.$currentLanguage = this._store.select((state: IEditorState) => {return state.siteEditor.currentLanguage});
     this._currentLanguageSubscription = this.$currentLanguage.subscribe((currentLanguage: ILanguageResource) => {
      this._currentLanguage = currentLanguage;
    });


  }

  public readImage(input): Subject<IImageFile> {
    let subject = new Subject<IImageFile>();
    let reader = new FileReader();

    reader.addEventListener("loadend", (readerEvent) => {
      let originalImage = new Image();
      let maxSize = 1800;
      originalImage.onload = () => {
        originalImage.alt = input.target.files[0] ? input.target.files[0].type : "";
        if (originalImage.alt === "image/png") {
          originalImage.width = originalImage.width * 0.6;
          originalImage.height = originalImage.height * 0.6;
        }
        originalImage.width = originalImage.width > maxSize ? originalImage.width * (0.8 - (originalImage.width / 10000)) : originalImage.width;
        originalImage.height = originalImage.height > maxSize ? originalImage.height * (0.8 - (originalImage.height / 10000)) : originalImage.height;
        this.compressImage(originalImage.src, subject, originalImage);
      };
      originalImage.src = readerEvent.target["result"];
    });
    if (input.target.files[0]) {
      reader.readAsDataURL(input.target.files[0]);
    }

    return subject;
  }

  public uploadImage(projectId: string, imageData, name?: string): Subject<string> {
    let subject = new Subject<string>();
    let fileId = name || Global.newGuid();
    this._imagesService.uploadImage(projectId, this._currentLanguage.id , fileId, imageData, imageData.type).subscribe((imageUrl: string) => {
      subject.next(imageUrl);
    });
    return subject;
  }


  private compressImage(imageRead, subject, originalImage) {
    let imageCompressor = new ImageCompressor;
    let compressorSettings = {
      mode: "strict",
      mimeType: originalImage.alt,
      quality: 0.3,
      toWidth: originalImage.width * 0.6,
      toHeight: originalImage.height * 0.6,
      speed: "high"
    };
    let blob = null;
    if (imageRead.length > maxLenght) {
      imageCompressor.run(imageRead, compressorSettings, (src) => {
        blob = this.b64toBlob(src.replace("data:" + originalImage.alt + ";base64,", ""), compressorSettings.mimeType, 512);
        subject.next({
          type: "imageResized",
          blob: blob,
          url: URL.createObjectURL(blob)
        });
      });
    } else {
      blob = this.b64toBlob(imageRead.replace("data:" + originalImage.alt + ";base64,", ""), compressorSettings.mimeType, 512);
      subject.next({
        type: "imageResized",
        blob: blob,
        url: URL.createObjectURL(blob)
      });
    }

  }

  private b64toBlob(b64Data, contentType, sliceSize) {
    contentType = contentType || "";
    sliceSize = sliceSize || 512;

    let byteCharacters = atob(b64Data);
    let byteArrays = [];

    for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
      let slice = byteCharacters.slice(offset, offset + sliceSize);

      let byteNumbers = new Array(slice.length);
      for (let i = 0; i < slice.length; i++) {
        byteNumbers[i] = slice.charCodeAt(i);
      }

      let byteArray = new Uint8Array(byteNumbers);

      byteArrays.push(byteArray);
    }
    let blob = new Blob(byteArrays, {type: contentType});
    return blob;
  }
}

