import { Injectable } from "@angular/core";
import { RestApiSevice } from "app/common/services/oauth/rest-api.service";
import { Cache, CacheService } from "app/common/decorators/cache";
import { Observable } from "rxjs";

@Injectable()
export class UserService {
    private _endpoints = {
        userPreferences: "api/user",
        firstAccess: "api/user/editorAccess",
        userprofile: "api/bizagi/userprofile"
    };

    constructor(protected _restApiService: RestApiSevice) {
    }

    public getUserProfile(url: string) {
        let response = this._restApiService.post({
            url: this._endpoints.userprofile,
            body: {
                url: url
            }
        });
        return response;
    }

    @Cache()
    public createUserPreferences(user: IUserProfile): Promise<any> {
        return this._restApiService.post({
            url: this._endpoints.userPreferences,
            body: user
        }).toPromise();
    }

    public validateFirstAccess(email: string): Promise<boolean> {
        return this._restApiService.get({
            url: `${this._endpoints.firstAccess}/${email}`
        }).toPromise();
    }

    @Cache()
    public getUserByEmail(email: string): Promise<any> {
        return this._restApiService.get({
            url: `${this._endpoints.userPreferences}/${email}`
        }).toPromise();
    }

    public getUserByUsername(username: string): Promise<any> {
        return this._restApiService.get({
            url: `${this._endpoints.userPreferences}/${username}`
        }).toPromise();
    }

    public createOrUpdateUserPreferences(user: any): Promise<any> {
        return this._restApiService.put({
            url: `${this._endpoints.userPreferences}/${user.userName || user.username}`,
            body: user
        }).toPromise();
    }
}
