import {Component} from "@angular/core";
import {BaseProcessControlComponent} from "app/common/controls/base-process.component";
import {ControlDefinition} from "app/common/decorators/control-definition";
import { CATEGORY, SUBCATEGORY_LINK, LINK_CONTROLS } from "./../controls-name-definition";

@Component({
  selector: "bs-link-control",
  templateUrl: "./link-control.component.html",
  styleUrls: ["./link-control.component.scss"]
})
@ControlDefinition(
  {
    type: LINK_CONTROLS.link.type,
    category: CATEGORY.BUTTONS.displayName,
    subcategory: SUBCATEGORY_LINK.displayName,
    displayName: LINK_CONTROLS.link.displayName,
    icon: LINK_CONTROLS.link.icon,
    styles: {
      fontFamily: {
        type: "font-family",
        defaultValue: "Open Sans"
      },
      fontSize: {
        type: "font-size",
        defaultValue: "18px",
        max: 72
      },
      fontColor: {
        type: "color",
        defaultValue: "color-second"
      },
      backColor: {
        type: "color",
        defaultValue: "rgba(0, 0, 0, 0)"
      },
      align: {
        type: "text-align",
        defaultValue:  "left"
      }
    },
    parameters: {
      text: {
        type: "title",
        defaultValue: "Enter your text"
      }
    },
    defaultSize: {
      width: 156,
      height: 33
    },
    links: {
      staticPages: true,
      internalPages: true,
      processes: true
    }
  }
)
export class LinkControlComponent extends BaseProcessControlComponent {

}
