import {CATEGORY, LINE_CONTROLS, SUBCATEGORY_LINE} from "./../controls-name-definition";
import {Component} from "@angular/core";
import {BaseControlComponent} from "app/common/controls/base.component";
import {ControlDefinition} from "app/common/decorators/control-definition";

@Component({
  selector: "bs-line-control",
  templateUrl: "./line-control.component.html",
  styleUrls: ["./line-control.component.scss"]
})
@ControlDefinition(
  {
    type: LINE_CONTROLS.line.type,
    category: CATEGORY.FIGURES.displayName,
    subcategory: SUBCATEGORY_LINE.displayName,
    displayName: LINE_CONTROLS.line.displayName,
    icon: LINE_CONTROLS.line.icon,
    styles: {
      fullWidth: {
        type: "boolean",
        defaultValue: false
      },
      opacity: {
        type: "range",
        defaultValue: 100,
        min: 0,
        max: 100
      },
      line: {
        type: "border",
        defaultValue: "4px solid #000"
      }
    },
    parameters: {
    },
    defaultSize: {
      width: 280,
      height: 45
    },
    hiddenOptions: {
        dataManager: true
      }
  }
)

export class LineControlComponent extends BaseControlComponent {

  checkIfFullWidth() {
    let parent = $("#" + this.control.id);
    let fullWidthClass = "--full-width";
    let hasFullWidthClass =  parent.hasClass(fullWidthClass);
    if (this.styles.fullWidth && !hasFullWidthClass ) {
      parent.addClass(fullWidthClass);
      return true;
    } else if (!this.styles.fullWidth && hasFullWidthClass) {
     parent.removeClass(fullWidthClass);
     return false
    }
    return false;
  }
}
