import { Component, EventEmitter, Input, Output } from "@angular/core";

@Component({
  selector: "bs-dm-template-dynamic",
  templateUrl: "./dm-template-dynamic.component.html",
  styleUrls: ["./dm-template-dynamic.component.scss"]
})
export class DmTemplateDynamicComponent {

  @Input() selectedField;
  public fields: { [key: string]: string };
  @Output() setFocus = new EventEmitter();
  @Output() changeDisableField = new EventEmitter();
  constructor() {
    this.fields = {
      image: "image",
      header_footer: "header_footer",
      title: "title",
      subtitle: "subtitle",
      body_1: "body_1",
      body_2: "body_2",
      body_3: "body_3",
      heading_1: "heading_1",
      heading_2: "heading_2",
      caption: "caption"
    }
  }

  changeField(field: string) {
    this.changeDisableField.emit(field);
  }

}
