import { Injectable } from "@angular/core";
import { Observable } from "rxjs/Rx";
import { HttpClient, HttpHeaders } from "@angular/common/http";

@Injectable()
export class RestApiSevice {

  protected headers: HttpHeaders;
  protected access_token: string;

  constructor(private _http: HttpClient) {
    // Add default headers
    this.headers = new HttpHeaders();
    this.headers = this.headers.set("Content-Type", "application/json");
  }

  public setAuthenticationToken(access_token) {
    this.access_token = access_token;
    this.headers = this.headers.set("Authorization", "Bearer " + access_token);
  }

  public get(request: RestApiRequest | any, isOctetStream = false): Observable<any> {
    // Assertions
    if (!request.url) {
      throw new Error("URL is not set, please review your code");
    }

    let url = this.resolveUrlFromParameters(request);

    let headers = null;
    if (isOctetStream) {
      headers = _.cloneDeep(this.headers);
      headers = headers.set("content-type", "application/json");
      headers = headers.set("responseType", "application/octet-stream");
    }

    // Call http get
    return this._http.get(url, { headers: headers ? headers : this.headers })
  }

  public post(request: RestApiRequest | any): Observable<any> {
    // Assertions
    if (!request.url) {
      throw new Error("URL is not set, please review your code");
    }

    let url = this.resolveUrlFromParameters(request);

    // Call http post
    return this._http.post(url, JSON.stringify(request.body), { headers: this.headers });
  }

  public postFile(request: RestApiRequest | any, file, type) {

    let headers = _.cloneDeep(this.headers);
    if (type) {
      headers = headers.set("content-type", type);
    }

    // Assertions
    if (!request.url) {
      throw new Error("URL is not set, please review your code");
    }

    let url = this.resolveUrlFromParameters(request);

    // Call http get
    return this._http.post(url, file, { headers: headers });
  }

  public postBlob(request: RestApiRequest | any, contentTypeValue?: string) {
    let headers = _.cloneDeep(this.headers);
    headers = headers.set("content-type", "application/json");
    headers = headers.set("responseType", contentTypeValue ? contentTypeValue : "Blob");

    if (!request.url) {
      throw new Error("URL is not set, please review your code");
    }

    if (!request.body) {
      throw new Error("Body is not set, please review your code");
    }

    let url = this.resolveUrlFromParameters(request);

    return this._http.post(url, request.body, { headers: headers, responseType: "blob" });
  }

  public postFormData(request: RestApiRequest | any) {
    let headersSet = new HttpHeaders({
      "Authorization": this.headers.get("Authorization")
    });

    if (!request.url) {
      throw new Error("URL is not set, please review your code");
    }

    if (!request.body) {
      throw new Error("Body is not set, please review your code");
    }

    let url = this.resolveUrlFromParameters(request);

    return this._http.post(url, request.body, { headers: headersSet });
  }

  public put(request: RestApiRequest | any): Observable<any> {
    // Assertions
    if (!request.url) {
      throw new Error("URL is not set, please review your code");
    }

    let url = this.resolveUrlFromParameters(request);

    // Call http put
    return this._http.put(url, JSON.stringify(request.body), { headers: this.headers });
  }

  public delete(request: RestApiRequest | any): Observable<any> {
    // Assertions
    if (!request.url) {
      throw new Error("URL is not set, please review your code");
    }

    let url = this.resolveUrlFromParameters(request);

    // Call http delte
    return this._http.delete(url, { headers: this.headers });
  }

  public patch(request: RestApiRequest | any) {
    // Assertions
    if (!request.url) {
      throw new Error("URL is not set, please review your code");
    }

    let url = this.resolveUrlFromParameters(request);

    // Call http get
    return this._http.patch(url, JSON.stringify(request.body), { headers: this.headers })
  }

  private resolveUrlFromParameters(request: RestApiRequest | any): string {
    // Create url changing path and query string
    let url = request.url;
    if (request.parameters) {
      for (let k in request.parameters) {
        if (request.parameters.hasOwnProperty(k)) {
          url = url.replace("{" + k + "}", request.parameters[k]);
        }
      }
    }
    if (request.queryParams) {
      url = `${url}?${this.buildQuery(request.queryParams)}`;
    }
    return url;
  }

  private buildQuery(params) {
    const filterArray = params ? Object.keys(params)
        .filter(paramKey => `${params[paramKey]}`)
        .map(paramKey => `${paramKey}=${params[paramKey]}`) : [];
    return filterArray.length > 0 ? `${filterArray.join("&")}` : "";
  };
}

class RestApiRequest {
  url: string;
  parameters: Array<any>;
  body: any;
}

class AuthorizationError extends Error {

}

