import { Component } from "@angular/core";
import { BaseDataControlComponent } from "app/common/controls/base-data.component";
import { ControlDefinition } from "app/common/decorators/control-definition";
import { Global } from "app/common/misc/global";
import { CATEGORY, SUBCATEGORY_IMAGE_CAROUSEL, IMAGE_CAROUSEL_CONTROLS } from "./../controls-name-definition";
import { DefaultValues } from "../../misc/default-values";

@Component({
  selector: "bs-image-carousel-control",
  templateUrl: "./image-carousel-control.component.html",
  styleUrls: ["./image-carousel-control.component.scss"]
})
@ControlDefinition(
  {
    type: IMAGE_CAROUSEL_CONTROLS.imageCarousel.type,
    category: CATEGORY.BUNDLES.displayName,
    subcategory: SUBCATEGORY_IMAGE_CAROUSEL.displayName,
    displayName: IMAGE_CAROUSEL_CONTROLS.imageCarousel.displayName,
    icon: IMAGE_CAROUSEL_CONTROLS.imageCarousel.icon,
    styles: {
      designDisplay: {
        type: "design",
        defaultValue: "title-bottom",
        category: "hidden",
        displays: {
          "title-bottom": {
            icon: "image-gallery-display-bottom",
            text: "siteeditor.layouts.DESIGN_TITLE_BOTTOM",
          },
          "title-top": {
            icon: "image-gallery-display-top",
            text: "siteeditor.layouts.DESIGN_TITLE_TOP",
          }
        }
      },
      layout: {
        type: "layout",
        defaultValue: "three",
        category: "hidden",
       layouts: {
            "two": {
              icon: "two-columns-thin",
              text: "TWO_COLUMNS",
              columns: 2
            },
            "three": {
              icon: "three-columns-thin",
              text: "THREE_COLUMNS",
              min: 50,
              columns: 3
            },
            "four": {
              icon: "four-columns-thin",
              text: "FOUR_COLUMNS",
              min: 66,
              columns: 4
            }
          }
      },
      multipageItemView: {
        type: "radio",
        defaultValue: "Pagination",
        category: "layout",
        values: ["Pagination"]
      },
      verticalItemSpacing: {
        type: "range",
        defaultValue: 12,
        category: "layout",
        step: 4,
        min: 4,
        max: 40,
        unitValue: "px",
        headerName: "Rows"
      },
      displayedItems: {
        type: "range",
        defaultValue: 4,
        category: "layout",
        min: 1,
        max: 10
      },
      backColor: {
        type: "color",
        defaultValue: "#FFF"
      },
      boxShadow: {
        type: "text",
        defaultValue: "0 1px 1px 0 rgba(0,0,0,.5), 0 1px 6px 0 rgba(0,0,0,.02)"
      },
      border: {
        type: "border",
        defaultValue: "1px solid #e4e8ea"
      },
      cornerRadius: {
        type: "range",
        defaultValue: 0,
        min: 6,
        max: 50
      }
      },
    parameters: {
      data: {
        type: "data",
        size: 10
      }
    },
    inputs: ["parameters.filter"],
    defaultSize: {
      width: 747,
      height: 492,
      minWidth: 495,
      minHeight: 246,
      maxHeight: 492,
      dynamic: true
    },
    bizagiConnections: {
      cases: true,
      collections: true,
      searches: true,
      config: {
        image: "image-gallery-config.png",
        coords: {
          "image": {
            w: 56,
            h: 61,
            x: 10,
            y: 6
          },
          "title": {
            w: 56,
            h: 9,
            x: 10,
            y: 73
          }
        }
      }
    },
    fields: {
      image: {
        type: "image",
        displayName: "siteeditor.properties.IMAGE",
        styles: {
          imagesSize: {
            type: "image-size",
            defaultValue: "scale"
          }, opacity: {
            type: "range",
            defaultValue: 100,
            min: 0,
            max: 100
          },
          background: {
            type: "color",
            defaultValue: "rgba(0, 0, 0, 0)"
          },
          border: {
            type: "border",
            defaultValue: "border-none"
          }
        }
      },
      title: {
        type: "title",
        displayName: "siteeditor.properties.TITLE",
        styles: {
          fieldLabel: {
            type: "label-field",
            defaultValue: {
              isActive: false,
              text: "Label:"
            }
          },
          fontFamily: {
            type: "font-family",
            defaultValue: "Open Sans"
          },
          fontSize: {
            type: "font-size",
            defaultValue: "16px",
            max: 72
          },
          letterSpacing: {
            type: "range",
            defaultValue: 0,
            min: 0,
            max: 60
          },
          align: {
            type: "text-align",
            defaultValue: "left"
          },
          fontColor: {
            type: "color",
            defaultValue: "color-first"
          },
          backColor: {
            type: "color",
            defaultValue: "rgba(0, 0, 0, 0)"
          },
          fontWeight: {
            type: "font-weight",
            defaultValue: "normal"
          }
        }
      }
    },
    hiddenOptions: {
      modifyFields: true,
      designOption: false
    }
  }
)

export class ImageCarouselControlComponent extends BaseDataControlComponent {
  public selectedItem = 0;
  public defaultPicture = DefaultValues.getDefaultPicture();

  public nextItem() {
    this.selectedItem = this.selectedItem < this.data.length - 1 ? this.selectedItem + 1 : 0;
  }
  public prevItem() {
    this.selectedItem = this.selectedItem > 0 ? this.selectedItem - 1 : this.data.length - 1;
  }

  protected resize(event: any) {
    if (
      event.target.height > this.maxElementHeight &&
      this.controlDefinition.defaultSize.dynamic &&
      this.styles.multipageItemView === "Pagination") {
      this.maxElementHeight = event.target.height;
      let paginationsHeight = 40;
      const dynamicSizeItem = $("[data-control-id=" + this.control.id + "] .bs-selected-image");
      let itemHeight = dynamicSizeItem.outerHeight(true);
      const thumbnailHeight = $("[data-control-id=" + this.control.id + "] .bs-carousel-thumbnails").outerHeight(true);
      let heightElement = itemHeight + (thumbnailHeight * .6) + paginationsHeight;

      if (dynamicSizeItem && heightElement) {
        this.onResize.next({
          width: dynamicSizeItem.width(),
          height: heightElement
        });
      }
    }
  }
}
