import {BaseProcessControlComponent} from "app/common/controls/base-process.component";
import {ControlDefinition} from "app/common/decorators/control-definition";
import {Component} from "@angular/core";
import { CATEGORY, SUBCATEGORY_BUTTON_REGULAR, BUTTON_REGULAR_CONTROLS } from "./../controls-name-definition";

@Component({
  selector: "bs-button-control",
  templateUrl: "./button-control.component.html",
  styleUrls: ["./button-control.component.scss"]
})
@ControlDefinition(
  {
    type: BUTTON_REGULAR_CONTROLS.button.type,
    category: CATEGORY.BUTTONS.displayName,
    subcategory: SUBCATEGORY_BUTTON_REGULAR.displayName,
    displayName: BUTTON_REGULAR_CONTROLS.button.displayName,
    icon: BUTTON_REGULAR_CONTROLS.button.icon,
    styles: {
      fontFamily: {
        type: "font-family",
        defaultValue: "Open Sans"
      },
      fontSize: {
        type: "font-size",
        defaultValue: "14px",
        max: 72
      },
      fontColor: {
        type: "color",
        defaultValue: "color-sixth"
      },
      backColor: {
        type: "color",
        defaultValue: "color-second"
      },
      align: {
        type: "text-align",
        defaultValue:  "center"
      },
      iconLeft: {
        type: "boolean",
        defaultValue:  true
      },
      iconRight: {
        type: "boolean",
        defaultValue:  false
      },
      border: {
        type: "border",
        defaultValue: "border-btn"
      },
      borderRadius: {
        type: "range",
        defaultValue: 5,
        min: 0,
        max: 25
      }
    },
    parameters: {
      text: {
        type: "title",
        defaultValue: BUTTON_REGULAR_CONTROLS.button.displayName
      },
      iconSelector: {
        type: "icon-selector"
      }
    },
    defaultSize: {
      width: 170,
      height: 59
    },
    links: {
      staticPages: true,
      internalPages: true,
      processes: true
    }
  }
)
export class ButtonControlComponent extends BaseProcessControlComponent {
  public fontColor: string;
  public backColor: string;
}
