
import { first } from "rxjs/operators";
import { ChangeDetectorRef, Component, ViewChild } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { Global } from "app/common/misc/global";
import { PageRenderingService } from "app/common/services/sites/page-rendering.service";
import { PublishedSiteService } from "app/render/services/published-site.service";
import { BizagiDataService } from "app/render/services/bizagi-data.service";
import { ContextService } from "app/common/services/sites/context.service";
import { ThemeService } from "app/common/services/sites/theme.service";
import { BizagiFormsComponent, FormType } from "app/render/bizagi-forms/bizagi-forms.component";
import { UserService } from "app/common/services/sites/user.service";
import { Observable, Subscription } from "rxjs";
import { Store } from "@ngrx/store";
import { TranslateService } from "@ngx-translate/core";
import * as MultilanguageActions from "app/render/store/multilanguage.actions"
import {UpdateNavigationContexts} from "../store/navigation-contexts.actions";
import {UpdatePageContext} from "../store/page-context.actions";
import {UpdateGridSize} from "../store/grid.actions";

let objectFitImages = require("object-fit-images");
@Component({
  selector: "bs-page-render",
  templateUrl: "./page-render.component.html",
  styleUrls: ["./page-render.component.scss"]
})
export class PageRenderComponent {

  public controls: Array<IControl>;
  public page: IPage;
  public pagesList: Array<IBasicPage>;
  public site: ISite;
  public user: IUserProfile;
  public userSite: IUserSiteProfile;
  public isLoadingPreferences = false;
  public navigationContexts: Array<INavigationContext>;
  public resources: ISiteResource[]

  private _currentLanguage$: Observable<ILanguageResource>;
  public currentLanguage: ILanguageResource;

  // Form settings
  @ViewChild("form", { static: false })
  private form: BizagiFormsComponent;
  private formParameters: IFormRenderingParams = {
    connection: "",
    title: "",
    type: FormType.New,
    parameters: {},
    refresh: null
  };

  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _publishedSiteServices: PublishedSiteService,
    private _pageRenderingService: PageRenderingService,
    private _bizagiDataServices: BizagiDataService,
    private _contextServices: ContextService,
    private _cdr: ChangeDetectorRef,
    private _themeServices: ThemeService,
    private _userService: UserService,
    private _store: Store<IRenderState>,
    private _translateService: TranslateService
  ) {

  }

  ngOnInit() {
    this._route.params.subscribe((params) => {
      this.site = null;
      this.page = null;
      this.controls = [];
      this._cdr.detectChanges();

      let siteName = params["siteName"];
      let pageName = params["pageName"];
      this.loadSiteAndPage(siteName, pageName);

      this._currentLanguage$ = this._store.select((s: IRenderState) => { return s.currentLanguage; });
      this._currentLanguage$.subscribe((currentLanguage: ILanguageResource) => {
        if (currentLanguage) {
          this.currentLanguage = currentLanguage;
        }
      });
    }
    );
    // Attach to rendering service events
    this._pageRenderingService.on("showForm", (parameters) => this.showForm(parameters));
  }

  ngAfterViewInit() {
    objectFitImages();
  }

  public updateUserPreferences(user: IUserSiteProfile) {
    this.isLoadingPreferences = true;
    setTimeout(() => {
      this.isLoadingPreferences = false;
    }, 500);
  }
  private async loadPage(siteName: string, pageName: string) {
    try {
      let page = await this._publishedSiteServices.getPage(siteName, pageName);
      if (page.grid) {
        this._store.dispatch(new UpdateGridSize(page.grid.size));
      }
      let url = (<Array<IBizagiProject>>_.values(this.site.environments[this.site.defaultEnvironment].projects))[0].url;
      let stakeholders = await this._bizagiDataServices.getStakeholders(url);
      _.each(stakeholders, (stakeholder: IStakeholder) => {
        _.each(stakeholder.contexts, (context: IStakeholderContext) => {
          this.user.contexts = [context.id, ...this.user.contexts];
        });
      });
      if (Global.isVisibleTo(page.visibleTo, this.user.contexts)) {
        this.page = page;
        this._contextServices.setPage(this.page);
        this.controls = Global.getControlsByStakeholderContext(this.user.contexts, this.page.controls);
        this._route.queryParams.pipe(first()).subscribe((queryParams) => {
          let project = queryParams["project"] || _.first(_.keys(this.site.environments[this.site.defaultEnvironment].projects));
          let contextStr = queryParams["context"];
          if (project && contextStr) {
            this.processNavigationContext(project, contextStr);
          } else {
            this._contextServices.clearNavigationContext();
          }
        });
      } else {
        throw new Error("not visible to the user");
      }
    } catch (err) {
    }
  }

  private async loadSiteAndPage(siteName: string, pageName: string) {
    try {
      this.site = await this._publishedSiteServices.getSite(siteName);
      await this._getCurrentResourceFromBrowser();
      this._themeServices.loadSiteTheme(this.site.theme);
      this._contextServices.setSite(this.site);
      if (this._bizagiDataServices) {
        this.user = await this._bizagiDataServices.getUserProfile(this.site.identityServer);
        let userFromSites = await this._userService.getUserByEmail(this.user.userName);
        if (userFromSites) {
          let notPreferencesCustom = !userFromSites.preferences;
          let defaultPreferences = _.cloneDeep(this.currentLanguage.culture);
          let preferences = notPreferencesCustom
            ? _.cloneDeep(this.currentLanguage.culture)
            : (userFromSites.preferences.isCustom ? userFromSites.preferences : defaultPreferences);
          preferences.isCustom = !notPreferencesCustom ? userFromSites.preferences.isCustom : false;
          userFromSites.preferences = preferences;
          this.userSite = <IUserSiteProfile>userFromSites;
        } else {
          let newUserSites = { ...this.user, preferences: this.currentLanguage.culture }
          this.userSite = <IUserSiteProfile>(await this._userService.createOrUpdateUserPreferences(newUserSites));
        }
        if (Global.isBizagiImage(this.user.picture)) {
          this.user.picture = Global.getBizagiImageUrl(this.user.picture);
        }
        this.user.contexts = [];
        let tmpPages = await this._publishedSiteServices.getPages(siteName);
        this._contextServices.setPages(tmpPages);
        _.forEach(tmpPages, (page: IBasicPage) => {
          if (!page.originalId) {
            page.originalId = page.id;
          }
        });
        this.pagesList = tmpPages;
        this.loadPage(siteName, pageName);
      }
    } catch (err) {
      console.log(err);
    }
  }

  // Form management
  private showForm(parameters: IFormRenderingParams) {
    this.formParameters = parameters;

    // This is to allow angular to redraw the view and add the parameters to the form EditTemplateComponent
    // Sound weird isn"t it?
    setTimeout(() => {
      this.form.show();
      this.form.registerCloseFunction(this.formParameters.refresh);
    });
  }

  private async processNavigationContext(project: string, contextStr: string) {

    if (!project || !contextStr) {
      return;
    }

    // Get all context parts
    this.navigationContexts = <Array<INavigationContext>>Global.base64ToJson(contextStr);
    this._contextServices.setNavigationContext(this.navigationContexts);
    this._store.dispatch(new UpdateNavigationContexts(this.navigationContexts));

    let url = this._contextServices.getBizagiProjectUrl(project);
    let data = await this._bizagiDataServices.getNavigationData(url, this.navigationContexts);

    if (data) {
      this.page.context.entity.value = data.id;
      let pageData = _.mapValues(_.keyBy(data["fields"], "xpath"), "value");
      this._store.dispatch(new UpdatePageContext(this.page.context));
      this._contextServices.setPageData(pageData);
    }


    this.page.isContextualized = true;
  }

  private async _getCurrentResourceFromBrowser() {
    if (!this.currentLanguage) {
      this.resources = await this._publishedSiteServices.getResources(this.site.id);
      let browserLang = this._translateService.getBrowserLang();
      let currentLang = this.resources.find((c) => c.language.name === browserLang.substring(0, 2));

      if (currentLang) {
        this.currentLanguage = await this._publishedSiteServices.getResource(currentLang.id);
      } else {
        this.currentLanguage = await this._publishedSiteServices.getResource(this.site.globalSettings.defaultResource);
      }
      this._store.dispatch(new MultilanguageActions.UpdateCurrentLanguage(this.currentLanguage));
    }
  }
}
