import {Component, ElementRef, EventEmitter, Input, Output} from "@angular/core";
import {TrackingService} from "app/common/services/sites/tracking.service";

@Component({
  selector: "bs-modal-webparts",
  templateUrl: "./modal-webparts.component.html",
  styleUrls: ["./modal-webparts.component.scss"]
})
export class ModalWebPartsComponent {

  @Input() title: string;
  @Input() buttons: Array<IButton>;
  @Input() globalSettings: IGlobalSetting;
  @Input() isShowGoDown = false;
  @Output() onClose = new EventEmitter();
  @Output() onGoDown = new EventEmitter();
  @Output() onButtonClick = new EventEmitter();

  private modal;
  private dialog;
  private header;
  private footer;

  public isLoading: string;

  constructor(private elementReference: ElementRef) {

  }

  ngOnInit() {
    this.modal = $(".bs-modal", this.elementReference.nativeElement);
    this.dialog = $(".bs-dialog", this.elementReference.nativeElement);
    this.header = $(".bs-dialog-header", this.elementReference.nativeElement);
    this.footer = $(".bs-dialog-footer", this.elementReference.nativeElement);
  }

  public open() {
    this.modal.addClass("open");
    $("body").addClass("--no-scroll --modal");
  }

  public close() {
    this.modal.removeClass("open");
    $("body").removeClass("--no-scroll --modal");
    this.onClose.emit();
  }

  public loading(button) {
    this.isLoading = button;
  }

  public goDown() {
    this.onGoDown.next(null);
  }

  public buttonClicked(action: string) {
    this.onButtonClick.emit(action);
  }
}
