import * as NavigationContextsActions from "./navigation-contexts.actions";

export function navigationContextsReducer (state = [], action: NavigationContextsActions.UpdateNavigationContexts) {
  switch (action.type) {
    case NavigationContextsActions.UPDATE_NAVIGATION_CONTEXTS: {
      let navigationContexts: Array<INavigationContext> = action.payload;
      return [
        ...state,
        ...navigationContexts
      ]
    }
    default:
      return state

  }

}

