import {Component, EventEmitter, Input, Output} from "@angular/core";

@Component({
  selector: "bs-dm-template-gallery-link",
  templateUrl: "./dm-template-gallery-link.component.html",
  styleUrls: ["./dm-template-gallery-link.component.scss"]
})
export class DmTemplateGalleryLinkComponent {

  @Input() selectedField;
  @Output() setFocus = new EventEmitter();
  @Output() changeDisableField = new EventEmitter();

  constructor() { }

  changeField(field: string) {
    this.changeDisableField.emit(field);
  }

}
