import { Component } from "@angular/core";
import { BaseDataControlComponent } from "app/common/controls/base-data.component";
import { ControlDefinition } from "app/common/decorators/control-definition";
import { CATEGORY, SUBCATEGORY_LIST_VIEW, LIST_VIEW_CONTROLS } from "./../controls-name-definition";
import { DefaultValues } from "../../misc/default-values";
import { Global } from "app/common/misc/global";

@Component({
  selector: "bs-list-view-control",
  templateUrl: "./list-view-control.component.html",
  styleUrls: ["./list-view-control.component.scss"]
})
@ControlDefinition(
  {
    type: LIST_VIEW_CONTROLS.listView.type,
    category: CATEGORY.BUNDLES.displayName,
    subcategory: SUBCATEGORY_LIST_VIEW.displayName,
    displayName: LIST_VIEW_CONTROLS.listView.displayName,
    icon: LIST_VIEW_CONTROLS.listView.icon,
    styles: {
      designCompressed: {
        type: "design",
        category: "design",
        defaultValue: "false"
      },
      designDisplay: {
        type: "design",
        defaultValue: "basic-right",
        category: "hidden",
        displays: {
          "basic-right": {
            icon: "dynamic_basic_right",
            text: "siteeditor.layouts.DESIGN_DISPLAY_RIGHT",
          },
          "basic-left": {
            icon: "dynamic_basic_left",
            text: "siteeditor.layouts.DESIGN_DISPLAY_LEFT",
          }
        }
      },
      designActions: {
        type: "design",
        defaultValue: "bottom",
        category: "hidden",
        actions: {
          "top": {
            icon: "dynamic_top",
            text: "siteeditor.layouts.DESIGN_ACTIONS_TOP",
          },
          "bottom": {
            icon: "dynamic_botton",
            text: "siteeditor.layouts.DESIGN_ACTIONS_BOTTOM",
          }
        }
      },
      layout: {
        type: "layout",
        defaultValue: "two",
        category: "hidden",
       layouts: {
            "one": {
              icon: "one-column-thin",
              text: "ONE_COLUMN",
              columns: 1
            },
            "two": {
              icon: "two-columns-thin",
              text: "TWO_COLUMNS",
              min: 50,
              columns: 2
            },
            "three": {
              icon: "three-columns-thin",
              text: "THREE_COLUMNS",
              min: 75,
              columns: 3
            }
          }
      },
      multipageItemView: {
        type: "radio",
        defaultValue: "Pagination",
        category: "layout",
        values: ["Pagination"]
      },
      verticalItemSpacing: {
        type: "range",
        defaultValue: 12,
        category: "layout",
        step: 4,
        min: 4,
        max: 40,
        unitValue: "px",
        headerName: "Rows"
      },
      displayedItems: {
        type: "range",
        defaultValue: 3,
        category: "layout",
        min: 1,
        max: 10
      },
      backColor: {
        type: "color",
        defaultValue: "#FFF"
      },
      boxShadow: {
        type: "text",
        defaultValue: "0 1px 1px 0 rgba(0,0,0,.5), 0 1px 6px 0 rgba(0,0,0,.02)"
      },
      border: {
        type: "border",
        defaultValue: "1px solid #e4e8ea"
      },
      cornerRadius: {
        type: "range",
        defaultValue: 0,
        min: 6,
        max: 50
      },
      showActions: {
        type: "boolean",
        defaultValue: "true"
      }
    },
    parameters: {
      data: {
        type: "data",
        size: 10
      },
      filter: {
        type: "text",
        defaultValue: "",
        isFilter: true
      }
    },
    inputs: ["parameters.filter"],
    defaultSize: {
      width: 950,
      height: 620,
      minWidth: 260,
      minHeight: 145,
      dynamic: true
    },
    bizagiConnections: {
      cases: true,
      collections: true,
      searches: true,
      config: {
        image: "list-view-config.png",
        coords: {
          "image": {
            w: 75,
            h: 75,
            x: 7,
            y: 7
          },
          "title": {
            w: 136,
            h: 13,
            x: 90,
            y: 7
          },
          "text": {
            w: 180,
            h: 10,
            x: 90,
            y: 30
          },
          "info": {
            w: 80,
            h: 18,
            x: 190,
            y: 50
          }
        }
      }
    },
    fields: {
      image: {
        type: "image",
        displayName: "siteeditor.properties.IMAGE",
        styles: {
          imagesSize: {
            type: "image-size",
            defaultValue: "scale"
          }, opacity: {
            type: "range",
            defaultValue: 100,
            min: 0,
            max: 100
          },
          background: {
            type: "color",
            defaultValue: "rgba(0, 0, 0, 0)"
          },
          border: {
            type: "border",
            defaultValue: "border-none"
          }
        }
      },
      title: {
        type: "title",
        displayName: "siteeditor.properties.TITLE",
        styles: {
          fieldLabel: {
            type: "label-field",
            defaultValue: {
              isActive: false,
              text: "Label: "
            }
          },
          fontFamily: {
            type: "font-family",
            defaultValue: "Open Sans"
          },
          fontSize: {
            type: "font-size",
            defaultValue: "28px",
            max: 72
          },
          letterSpacing: {
            type: "range",
            defaultValue: 0,
            min: 0,
            max: 60
          },
          align: {
            type: "text-align",
            defaultValue: "left"
          },
          fontColor: {
            type: "color",
            defaultValue: "color-first"
          },
          backColor: {
            type: "color",
            defaultValue: "rgba(0, 0, 0, 0)"
          },
          fontWeight: {
            type: "font-weight",
            defaultValue: "normal"
          }
        }
      },
      text: {
        type: "text",
        displayName: "siteeditor.properties.TEXT",
        styles: {
          fieldLabel: {
            type: "label-field",
            defaultValue: {
              isActive: false,
              text: "Label: "
            }
          },
          fontFamily: {
            type: "font-family",
            defaultValue: "Open Sans"
          },
          fontSize: {
            type: "font-size",
            defaultValue: "12px",
            max: 72
          },
          letterSpacing: {
            type: "range",
            defaultValue: 0,
            min: 0,
            max: 60
          },
          align: {
            type: "text-align",
            defaultValue: "left"
          },
          fontColor: {
            type: "color",
            defaultValue: "color-sixth"
          },
          backColor: {
            type: "color",
            defaultValue: "rgba(0, 0, 0, 0)"
          },
          fontWeight: {
            type: "font-weight",
            defaultValue: "normal"
          }
        }
      },
      caption: {
        type: "title",
        displayName: "siteeditor.properties.CAPTION",
        styles: {
          fieldLabel: {
            type: "label-field",
            defaultValue: {
              isActive: false,
              text: "Label: "
            }
          },
          fontFamily: {
            type: "font-family",
            defaultValue: "Open Sans"
          },
          fontSize: {
            type: "font-size",
            defaultValue: "28px",
            max: 72
          },
          letterSpacing: {
            type: "range",
            defaultValue: 0,
            min: 0,
            max: 60
          },
          align: {
            type: "text-align",
            defaultValue: "left"
          },
          fontColor: {
            type: "color",
            defaultValue: "color-first"
          },
          backColor: {
            type: "color",
            defaultValue: "rgba(0, 0, 0, 0)"
          },
          fontWeight: {
            type: "font-weight",
            defaultValue: "normal"
          }
        }
      }
    },
    hiddenOptions: {
      modifyFields: true,
      designOption: false
    }
  }
)
export class ListViewControlComponent extends BaseDataControlComponent {
  public isOnRenderMode = Global.isOnRenderMode();
  public defaultPicture = DefaultValues.getDefaultPicture();
}
