import { Directive, ElementRef, Input, OnChanges, SimpleChanges } from "@angular/core";

@Directive({
  selector: "[bsDynamicControlEllipsis]"
})
export class DynamicControlEllipsisDirective {

  @Input() isOnRenderMode = false;

  constructor(private el: ElementRef) {
  }

  ngAfterViewInit() {

    if (this.isOnRenderMode) {
      if ($(this.el.nativeElement).hasClass("bs-bodytext-section__text")) {
        if (this.el.nativeElement.previousElementSibling) {
          $(this.el.nativeElement.previousElementSibling).css({ display: "none" });
        }
        $(this.el.nativeElement.nextElementSibling).css({ display: "none" });
        let element = $(this.el.nativeElement)
          .clone()
          .css({ display: "inline", lineHeight: "16px", visibility: "hidden" })
          .appendTo(this.el.nativeElement.parentElement);

        $(element).removeClass("bs-dynamic-control__ellipsis-height");

        if (element.height() > $(this.el.nativeElement).height() && this.el.nativeElement.nextElementSibling) {
          this.el.nativeElement.nextElementSibling.className = "bs-view-more";
          $(this.el.nativeElement.nextElementSibling).addClass("bs-view-more--type-body");
          $(this.el.nativeElement.parentElement).addClass("bs-view-more--parent-wview");
        } else {
          if (this.el.nativeElement.nextElementSibling) {
            this.el.nativeElement.nextElementSibling.className = "bs-view-more--display-none";
          }
        }
        if (this.el.nativeElement.previousElementSibling) {
          $(this.el.nativeElement.previousElementSibling).css({ display: "block" });
        }

        element.remove();
      } else if (!$(this.el.nativeElement).hasClass("bs-bodytext-section__text")) {

        if (this.el.nativeElement.previousElementSibling) {
          $(this.el.nativeElement.previousElementSibling).css({ display: "none" });
        }
        let element = $(this.el.nativeElement)
          .clone()
          .css({ display: "inline", visibility: "hidden" })
          .appendTo("body");

        $(element).removeClass("bs-dynamic-control__ellipsis");

        if (this.el.nativeElement.nextElementSibling && element.width() > $(this.el.nativeElement).width()) {
          this.el.nativeElement.nextElementSibling.className = "bs-view-more";
          $(this.el.nativeElement.parentElement).addClass("bs-view-more--parent-wview");
        } else {
          if (this.el.nativeElement.nextElementSibling) {
            this.el.nativeElement.nextElementSibling.className = "bs-view-more--display-none";
          }
        }

        if (this.el.nativeElement.previousElementSibling) {
          $(this.el.nativeElement.previousElementSibling).css({ display: "block" });
        }

        element.remove();
      }
    }



  }

  isEllipsisActive(element) {
    return (element.offsetHeight < element.scrollHeight ||
      element.offsetWidth < element.scrollWidth);
  }

}
