import { Component, Input } from "@angular/core";
import { PublishedSiteService } from "../services/published-site.service";
import { ContextService } from "app/common/services/sites/context.service";
import {Store} from "@ngrx/store";
import * as MultilanguageActions from "app/render/store/multilanguage.actions"

@Component({
  selector: "bs-languages-menu-render",
  templateUrl: "./languages-menu-render.component.html",
  styleUrls: ["./languages-menu-render.component.scss"]
})
export class LanguagesMenuRenderComponent {
  public isOpenedLangList = false;
  public site: ISite;
  @Input() public currentLanguage;
  @Input() resources: ISiteResource[];

  constructor(private _publishedSiteService: PublishedSiteService,
              private _contextService: ContextService,
              private _store: Store<IRenderState>) {
  }

  async ngOnInit() {
    this.site = this._contextService.getSite();
  }

  async changeSelectedResource(resource: ISiteResource) {
    let currentResource = await this._publishedSiteService.getResource(resource.id);
    this._store.dispatch(new MultilanguageActions.UpdateCurrentLanguage(currentResource));
    this.site.globalSettings.currentResource = currentResource.id;
    this.currentLanguage = currentResource;
    this.isOpenedLangList = false;
  }

}
