import {Component} from "@angular/core";
import {ControlDefinition} from "app/common/decorators/control-definition";
import {BaseControlComponent} from "app/common/controls/base.component";
import { CATEGORY, SUBCATEGORY_HEADER, HEADER_CONTROLS } from "./../controls-name-definition";

@Component({
  selector: "bs-subtitle-regular-control",
  templateUrl: "./subtitle-regular-control.component.html",
  styleUrls: ["./subtitle-regular-control.component.scss"]
})

@ControlDefinition(
  {
    type: HEADER_CONTROLS.subtitleRegular.type,
    category: CATEGORY.TEXT.displayName,
    subcategory: SUBCATEGORY_HEADER.displayName,
    displayName: HEADER_CONTROLS.subtitleRegular.displayName,
    icon: HEADER_CONTROLS.subtitleRegular.icon,
      styles: {
        fontFamily: {
          type: "font-family",
          defaultValue: "Open Sans"
        },
        fontSize: {
          type: "font-size",
          defaultValue: "20px",
          max: 72
        },
        letterSpacing: {
          type: "range",
          defaultValue: 0,
          min: 0,
          max: 60
        },
        align: {
          type: "text-align",
          defaultValue: "left"
        },
        fontColor: {
          type: "color",
          defaultValue: "color-first"
        },
        backColor: {
          type: "color",
          defaultValue: "rgba(0, 0, 0, 0)"
        },
        bold: {
          type: "boolean",
          defaultValue: false
        }

      },
      parameters: {
        text: {
          type: "title",
          defaultValue: HEADER_CONTROLS.subtitleRegular.displayName
        }
      },
      defaultSize: {
        width: 370,
        height: 53
      }
  }
)
export class SubtitleRegularControlComponent extends BaseControlComponent {

  fontWeight() {
    let bold = this.styles["bold"];
    return bold === true ? "600" : "100";
  }

}

