import { CATEGORY, SUBCATEGORY_HEADER, HEADER_CONTROLS } from "./../controls-name-definition";
import {Component} from "@angular/core";
import {BaseControlComponent} from "app/common/controls/base.component";
import {ControlDefinition} from "app/common/decorators/control-definition";
import {Global} from "../../misc/global";

@Component({
  selector: "bs-title-light-control",
  templateUrl: "./title-light-control.component.html",
  styleUrls: ["./title-light-control.component.scss"]
})
@ControlDefinition(
  {
    type: HEADER_CONTROLS.titleLight.type,
    category: CATEGORY.TEXT.displayName,
    subcategory: SUBCATEGORY_HEADER.displayName,
    displayName: HEADER_CONTROLS.titleLight.displayName,
    icon: HEADER_CONTROLS.titleLight.icon,
    styles: {
      fontFamily: {
        type: "font-family",
        defaultValue: "Open Sans"
      },
      fontSize: {
        type: "font-size",
        defaultValue: "24px",
        max: 72
      },
      letterSpacing: {
        type: "range",
        defaultValue: 0,
        min: 0,
        max: 60
      },
      align: {
        type: "text-align",
        defaultValue: "left"
      },
      fontColor: {
        type: "color",
        defaultValue: "color-first"
      },
      backColor: {
        type: "color",
        defaultValue: "rgba(0, 0, 0, 0)"
      },
      bold: {
        type: "boolean",
        defaultValue: false
      }

    },
    parameters: {
      text: {
        type: "title",
        defaultValue: HEADER_CONTROLS.titleLight.displayName
      }
    },
    defaultSize: {
      width: 280,
      height: 45
    }
  }
)

export class TitleLightControlComponent extends BaseControlComponent {

  fontWeight() {
    let bold = this.styles["bold"];
    return bold === true ? "600" : "100";
  }
}
