import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ComponentFactoryResolver,
  EventEmitter,
  Input,
  ReflectiveInjector,
  ViewChild,
  ViewContainerRef,
} from "@angular/core";
import { TranslateService } from "@ngx-translate/core";
import { ContextService } from "app/common/services/sites/context.service";
import { PageRenderingService } from "app/common/services/sites/page-rendering.service";
import { ActivatedRoute, Router } from "@angular/router";
import { BizagiDataService } from "app/render/services/bizagi-data.service";
import { ControlsService } from "app/common/services/sites/controls.service";
import { CultureSettingsService } from "app/common/services/sites/culture-settings.service";
import { BaseDataControlComponent } from "app/common/controls/base-data.component";
import { BaseProcessControlComponent } from "app/common/controls/base-process.component";
import { Store } from "@ngrx/store";
import { Observable, Subscription } from "rxjs";


// TODO: This and grid.panel.cell.component are almost the same, we nned to find a way to remove duplicate code
@Component({
  selector: "bs-grid-cell-renderer",
  template: "<div #gridrendercell></div>",
  changeDetection: ChangeDetectionStrategy.Default
})
export class GridCellRenderComponent {

  private _currentLanguage$: Observable<ILanguageResource>;
  private _currentLanguageSubscription: Subscription;
  private _pageContextSubscription: Subscription;

  @Input() currentLanguage: ILanguageResource;

  public onSetCurrentControl: EventEmitter<IControl> = new EventEmitter<IControl>();
  @ViewChild("gridrendercell", { read: ViewContainerRef, static: true }) viewContainerRef: ViewContainerRef;
  public currentComponent;

  @Input() set control(control: IControl) {
    if (!control) {
      return;
    }
    // Inputs need to be in the following format to be resolved properly
    let inputProviders = Object.keys(control).map((inputName) => { return { provide: inputName, useValue: control[inputName] }; });
    let resolvedInputs = ReflectiveInjector.resolve(inputProviders);

    // We create an injector out of the data we want to pass down and this components injector
    let injector = ReflectiveInjector.fromResolvedProviders(resolvedInputs, this.viewContainerRef.parentInjector);

    // We create a factory out of the component we want to create
    let controlComponent = this._controlsServices.getControlComponentByType(control.type);
    let factory = this._resolver.resolveComponentFactory(controlComponent);

    // We create the component using the factory and the injector
    let component = factory.create(injector);

    // We insert the component into the dom container
    this.viewContainerRef.insert(component.hostView);

    // We can destroy the old component is we like by calling destroy
    if (this.currentComponent && this.currentComponent.destroy) {
      this.currentComponent.destroy();
    }

    this.currentComponent = component.instance;


    if (this.currentComponent instanceof BaseDataControlComponent) {
      this.currentComponent.setStore(this._store);
      this.currentComponent.setServices(this._route,
        this._router,
        this._translateService,
        this._bizagiDataServices,
        this._pageRenderingService);

    } else if (this.currentComponent instanceof BaseProcessControlComponent) {
      this.currentComponent.setServices(this._pageRenderingService, this._bizagiDataServices);
    }

    this.currentComponent.setBaseServices(this._contextServices, null, this._cultureService);

    // Set the control information
    this.currentComponent.enableRenderingMode();
    this.currentComponent.setCurrentLanguage(this.currentLanguage);
    this.currentComponent.setControl(control, this._changeDetectorRef);
    this.currentComponent.loadParameters(control.parameters, control.parametersBinding);
    this._startSupscription(control);
  }

  constructor(private _resolver: ComponentFactoryResolver,
    private _controlsServices: ControlsService,
    private _bizagiDataServices: BizagiDataService,
    private _route: ActivatedRoute,
    private _router: Router,
    private _changeDetectorRef: ChangeDetectorRef,
    private _pageRenderingService: PageRenderingService,
    private _contextServices: ContextService,
    private _translateService: TranslateService,
    private _store: Store<IRenderState>,
    private _cultureService: CultureSettingsService) {
  }

  ngOnDestroy() {
    this._currentLanguageSubscription.unsubscribe();
    if (this._pageContextSubscription) {
      this._pageContextSubscription.unsubscribe();
    }
  }

  public setCurrentControl(control: IControl) {
    this.onSetCurrentControl.emit(control);
  }

  private _startSupscription(control: IControl) {
    this._currentLanguage$ = this._store.select((s: IRenderState) => {
      return s.currentLanguage;
    });
    // Current language state suscription
    this._currentLanguageSubscription = this._currentLanguage$.subscribe((currentLanguage: ILanguageResource) => {
      this.currentComponent.setCurrentLanguage(currentLanguage);
      // Load parameters
      this.currentComponent.loadParameters(control.parameters, control.parametersBinding);
      if (this.currentComponent.controlDefinition.category === "bundle" || this.currentComponent.controlDefinition.subcategory === "Inbox") {
        if (location.href.indexOf("parameters.filter") < 0) {
          this.currentComponent.refresh();
        }
        if (location.href.indexOf("parameters.filter") >= 0 && location.href.indexOf(this.currentComponent.control.id) < 0) {
          this.currentComponent.refresh();
        }
      }
    });

    if (this.currentComponent instanceof BaseDataControlComponent) {
      this._pageContextSubscription = this._store.select((s: IRenderState) => s.pageContext).subscribe(() => {
        if (location.href.indexOf("parameters.filter") < 0) {
          this.currentComponent.refresh();
        }
        if (location.href.indexOf("parameters.filter") >= 0 && location.href.indexOf(this.currentComponent.control.id) < 0) {
          this.currentComponent.refresh();
        }
      });
    }
  }
}
