import {Injectable} from "@angular/core";
import {Subject} from "rxjs";

@Injectable()
export class PageRenderingService {

  private notifyEvents: { [event: string]: Subject<any> } = {};


  public showForm(parameters: IFormRenderingParams ) {
    this.trigger("showForm", parameters);
  }

  public on(event: string, callback: Function) {
    if (!this.notifyEvents[event]) {
      this.notifyEvents[event] = new Subject<any>();
    }
    this.notifyEvents[event].subscribe((parameters) => {
      callback(parameters);
    });
  }

  public trigger(event: string, parameters: any) {
    if (!this.notifyEvents[event]) {
      this.notifyEvents[event] = new Subject<any>();
    }
    this.notifyEvents[event].next(parameters);
  }

}
