import {
  CATEGORY,
  RECTANGLE_CONTROLS,
  SUBCATEGORY_RECTANGLE
} from "./../controls-name-definition";
import {Component} from "@angular/core";
import {BaseControlComponent} from "app/common/controls/base.component";
import {ControlDefinition} from "app/common/decorators/control-definition";

@Component({
  selector: "bs-rectangle-control",
  templateUrl: "./rectangle-control.component.html",
  styleUrls: ["./rectangle-control.component.scss"]
})
@ControlDefinition(
  {
    type: RECTANGLE_CONTROLS.rectangle.type,
    category: CATEGORY.FIGURES.displayName,
    subcategory: SUBCATEGORY_RECTANGLE.displayName,
    displayName: RECTANGLE_CONTROLS.rectangle.displayName,
    icon: RECTANGLE_CONTROLS.rectangle.icon,
    styles: {
      fullWidth: {
        type: "boolean",
        defaultValue: false
      },
      color: {
        type: "color",
        defaultValue: "none"
      },
      opacity: {
        type: "range",
        defaultValue: 100,
        min: 0,
        max: 100
      },
      cornerRadius: {
        type: "range",
        defaultValue: 0,
        min: 0,
        max: 50
      },
      border: {
        type: "border",
        defaultValue: "1px solid #000"
      }
    },
    parameters: {
    },
    defaultSize: {
      width: 400,
      height: 200
    },
    hiddenOptions: {
      dataManager: true
    }
  }
)

export class RectangleControlComponent extends BaseControlComponent {

  checkIfFullWidth() {
    let parent = $("#" + this.control.id);
    let fullWidthClass = "--full-width";
    let hasFullWidthClass =  parent.hasClass(fullWidthClass);
    if (this.styles.fullWidth && !hasFullWidthClass ) {
      parent.addClass(fullWidthClass);
      return true;
    } else if (!this.styles.fullWidth && hasFullWidthClass) {
      parent.removeClass(fullWidthClass);
      return false
    }
    return false;
  }}
