import { Injectable } from "@angular/core";
import { Observable } from "rxjs/Rx";
import { Subject } from "rxjs";
import { Global } from "../../misc/global";

@Injectable()
export class ContextService {
  private _projectId: string;
  private _subscriptionId: string;
  private _site: ISite;
  private _page: IPage;
  private _pages: Array<IBasicPage>;
  private _navigationContext: Array<INavigationContext> = [];
  private _pageData: any;
  private _pageDataSubject: Subject<any>;
  private _siteVersion: string;

  public setSubscriptionId(subscriptionId: string) {
    this._subscriptionId = subscriptionId;
  }

  public getSubscriptionId(): string {
    return this._subscriptionId;
  }

  public setProjectId(projectId: string) {
    this._projectId = projectId;
  }

  public getProjectId(): string {
    return this._projectId;
  }

  public setSite(site: ISite) {
    this._site = site;
  }

  public getSite(): ISite {
    return this._site;
  }

  public setPage(page: IPage) {
    this._page = page;
    this._pageDataSubject = new Subject();
  }

  public getPage(): IPage {
    return this._page;
  }

  public setPages(pages: Array<IBasicPage>) {
    this._pages = pages;
  }

  public getPages() {
    return this._pages;
  }

  public getBizagiProjectUrl(bizagiProjectId: string) {
    return this._site.environments[this._site.defaultEnvironment].projects[bizagiProjectId].url;
  }

  public addNavigationContext(context: any) {
    this._navigationContext.push(context);
  }

  public clearNavigationContext() {
    this._navigationContext = [];
  }

  public getNavigationContext() {
    return this._navigationContext;
  }

  public setNavigationContext(navigationContexts: Array<INavigationContext>) {
    this._navigationContext = navigationContexts;
  }

  public lastNavigationContext() {
    return _.last(this._navigationContext);
  }

  public popNavigationContext() {
    return this._navigationContext.pop();
  }

  public setSiteVersion(siteVersion: string) {
    this._siteVersion = siteVersion;
  }

  public getSiteVersion() {
    return this._siteVersion;
  }

  public setPageData(data: any) {
    setTimeout(() => {
      this._pageData = data;
      this._pageDataSubject.next(this._pageData);
    }, 0);
  }

  public getPageData(): Observable<any> {
    return this._pageDataSubject;
  }

}
