import { Component, ViewChild, Input } from "@angular/core";
import { CultureSettingsService } from "app/common/services/sites/culture-settings.service";

@Component({
    selector: "bs-user-preferences",
    templateUrl: "./user-preferences.component.html",
    styleUrls: ["./user-preferences.component.scss"]
})
export class UserPreferencesComponent {
    public buttons = [];
    public formatOptions: ICultureFormatOptions;
    @Input() languageCulture: IUserPreferences;
    @Input() showDescription = true;

    constructor(private _cultureSettingsService: CultureSettingsService) {

    }

    ngOnInit() {
        this._cultureSettingsService.getFormatOptions().subscribe((cultureFormatOptions: ICultureFormatOptions) => {
          this.formatOptions = cultureFormatOptions;
        });
        this.buttons = [
          {
            name: "save",
            caption: "siteeditor.cultureconfig.SAVE"
          },
          {
            name: "cancel",
            caption: "siteeditor.cultureconfig.CLOSE",
            type: "secondary"
          }
        ]
    }

    public getUserPreferences() {
        return this.languageCulture;
    }

    public onChangeCheck(type: string, value: number) {
        switch (type) {
            case "dateFormatStyle":
                if (value !== this.languageCulture.dateFormatStyle) {
                    this.languageCulture.dateFormatStyle = value;
                }
                break;
            case "currencyPosition":
                if (value !== this.languageCulture.currencySymbolPosition) {
                    this.languageCulture.currencySymbolPosition = value;
                }
                break;
        }
    }

    public async refreshPreferences(preferences: IUserPreferences = null) {
        if ( preferences ) {
            this.languageCulture = preferences;
        } else {
            this.languageCulture = await this._cultureSettingsService.getPreferences().toPromise();
        }
    }

    public onChangeSelect(attr: string, value: any) {
        if (this.languageCulture[attr].toString() !== value) {
            if ( _.isNumber(value) ) {
                this.languageCulture[attr] = (<number>value);
            } else {
                this.languageCulture[attr] = value === " - " ? this.formatOptions[attr][1] : value;
            }
        }
    }

    public onChangeInputNumber(event: any) {
        let inputValue = (<number>event.target.value)
        if ( this.languageCulture.decimalDigits !== inputValue ) {
            this.languageCulture.decimalDigits = inputValue
        }
    }
}
